/**
 * Component Registry
 * Central registry for all components with auto-discovery and lazy loading capabilities
 */

import type { 
  ComponentMetadata, 
  ComponentRegistryEntry, 
  ComponentModule,
  ComponentConstructor,
  IComponent 
} from './component-metadata.js';
import { eventBus } from '../events/event-bus.js';

export type ComponentLoadEvent = {
  componentId: string;
  loadTime: number;
  success: boolean;
  error?: Error;
};

export type ComponentRegisteredEvent = {
  componentId: string;
  metadata: ComponentMetadata;
};

export type ModuleLoadedEvent = {
  moduleId: string;
  module: ComponentModule;
  componentCount: number;
};

export class ComponentRegistry {
  private static instance: ComponentRegistry;
  
  private components = new Map<string, ComponentRegistryEntry>();
  private modules = new Map<string, ComponentModule>();
  private loadingPromises = new Map<string, Promise<ComponentConstructor>>();
  
  private constructor() {}
  
  static getInstance(): ComponentRegistry {
    if (!ComponentRegistry.instance) {
      ComponentRegistry.instance = new ComponentRegistry();
    }
    return ComponentRegistry.instance;
  }

  /**
   * Register a component with metadata and lazy loader
   */
  registerComponent(
    metadata: ComponentMetadata,
    loader: () => Promise<ComponentConstructor>
  ): void {
    const entry: ComponentRegistryEntry = {
      metadata,
      loader,
      isLoaded: false,
      constructor: undefined
    };
    
    this.components.set(metadata.id, entry);
    
    eventBus.emit('component:registered', {
      componentId: metadata.id,
      metadata
    } as ComponentRegisteredEvent);
  }

  /**
   * Register multiple components from a module
   */
  registerModule(module: ComponentModule, components: Array<{
    metadata: ComponentMetadata;
    loader: () => Promise<ComponentConstructor>;
  }>): void {
    // Register the module
    this.modules.set(module.id, module);
    
    // Register all components in the module
    for (const component of components) {
      this.registerComponent(component.metadata, component.loader);
    }
    
    eventBus.emit('module:loaded', {
      moduleId: module.id,
      module,
      componentCount: components.length
    } as ModuleLoadedEvent);
  }

  /**
   * Get component metadata by ID
   */
  getComponentMetadata(componentId: string): ComponentMetadata | undefined {
    return this.components.get(componentId)?.metadata;
  }

  /**
   * Get all registered components
   */
  getAllComponents(): ComponentMetadata[] {
    return Array.from(this.components.values()).map(entry => entry.metadata);
  }

  /**
   * Get components by category
   */
  getComponentsByCategory(category: string): ComponentMetadata[] {
    return this.getAllComponents().filter(comp => comp.category === category);
  }

  /**
   * Search components by name, description, or tags
   */
  searchComponents(query: string): ComponentMetadata[] {
    const lowerQuery = query.toLowerCase();
    return this.getAllComponents().filter(comp => 
      comp.name.toLowerCase().includes(lowerQuery) ||
      comp.description?.toLowerCase().includes(lowerQuery) ||
      comp.tags?.some(tag => tag.toLowerCase().includes(lowerQuery))
    );
  }

  /**
   * Load a component (lazy loading)
   */
  async loadComponent(componentId: string): Promise<ComponentConstructor> {
    const entry = this.components.get(componentId);
    if (!entry) {
      throw new Error(`Component not found: ${componentId}`);
    }

    // Return if already loaded
    if (entry.isLoaded && entry.constructor) {
      return entry.constructor;
    }

    // Return existing loading promise if already in progress
    if (this.loadingPromises.has(componentId)) {
      return this.loadingPromises.get(componentId)!;
    }

    // Start loading
    const loadStartTime = performance.now();
    const loadingPromise = entry.loader()
      .then(constructor => {
        entry.constructor = constructor;
        entry.isLoaded = true;
        entry.loadedAt = new Date();
        
        const loadTime = performance.now() - loadStartTime;
        
        eventBus.emit('component:loaded', {
          componentId,
          loadTime,
          success: true
        } as ComponentLoadEvent);
        
        this.loadingPromises.delete(componentId);
        return constructor;
      })
      .catch(error => {
        const loadTime = performance.now() - loadStartTime;
        
        eventBus.emit('component:loaded', {
          componentId,
          loadTime,
          success: false,
          error
        } as ComponentLoadEvent);
        
        this.loadingPromises.delete(componentId);
        throw error;
      });

    this.loadingPromises.set(componentId, loadingPromise);
    return loadingPromise;
  }

  /**
   * Preload components by priority or category
   */
  async preloadComponents(options: {
    categories?: string[];
    maxPriority?: number;
    maxCount?: number;
  } = {}): Promise<void> {
    let componentsToLoad = this.getAllComponents();

    // Filter by categories
    if (options.categories) {
      componentsToLoad = componentsToLoad.filter(comp => 
        options.categories!.includes(comp.category)
      );
    }

    // Filter by priority
    if (options.maxPriority) {
      componentsToLoad = componentsToLoad.filter(comp => 
        (comp.loadPriority ?? 5) <= options.maxPriority!
      );
    }

    // Sort by priority (lower number = higher priority)
    componentsToLoad.sort((a, b) => (a.loadPriority ?? 5) - (b.loadPriority ?? 5));

    // Limit count
    if (options.maxCount) {
      componentsToLoad = componentsToLoad.slice(0, options.maxCount);
    }

    // Load components in parallel
    await Promise.allSettled(
      componentsToLoad.map(comp => this.loadComponent(comp.id))
    );
  }

  /**
   * Check if component has required dependencies loaded
   */
  checkDependencies(componentId: string): { satisfied: boolean; missing: string[] } {
    const metadata = this.getComponentMetadata(componentId);
    if (!metadata?.dependencies) {
      return { satisfied: true, missing: [] };
    }

    const missing: string[] = [];
    
    for (const dep of metadata.dependencies) {
      if (dep.type === 'component') {
        const depEntry = this.components.get(dep.id);
        if (!depEntry?.isLoaded && !dep.optional) {
          missing.push(dep.id);
        }
      }
      // TODO: Check service and module dependencies
    }

    return {
      satisfied: missing.length === 0,
      missing
    };
  }

  /**
   * Get component load statistics
   */
  getLoadStatistics(): {
    total: number;
    loaded: number;
    loading: number;
    unloaded: number;
    loadTimes: Record<string, number>;
  } {
    const entries = Array.from(this.components.values());
    const loadTimes: Record<string, number> = {};
    
    entries.forEach(entry => {
      if (entry.loadedAt) {
        // Calculate load time based on when component was loaded
        // This is a simplified version - in practice you'd track actual load times
        loadTimes[entry.metadata.id] = 0; // Placeholder
      }
    });

    return {
      total: entries.length,
      loaded: entries.filter(e => e.isLoaded).length,
      loading: this.loadingPromises.size,
      unloaded: entries.filter(e => !e.isLoaded).length,
      loadTimes
    };
  }

  /**
   * Get all registered modules
   */
  getAllModules(): ComponentModule[] {
    return Array.from(this.modules.values());
  }

  /**
   * Get module by ID
   */
  getModule(moduleId: string): ComponentModule | undefined {
    return this.modules.get(moduleId);
  }

  /**
   * Clear all registrations (for testing)
   */
  clear(): void {
    this.components.clear();
    this.modules.clear();
    this.loadingPromises.clear();
  }
}

// Export singleton instance
export const componentRegistry = ComponentRegistry.getInstance();