/**
 * Main Desktop Component
 * Renders the complete desktop environment including menu bar, dock/taskbar, and windows
 */

import { BaseComponent } from '../components/base/base-component.js';
import { serviceContainer, SERVICE_TOKENS } from '../core/di/service-container.js';
import { eventBus } from '../core/events/event-bus.js';
import type { 
  IWindowManager, 
  IDesktopService, 
  IAuthService,
  OSStyle,
  Theme,
  DesktopApp
} from '../core/types/index.js';
import type { UserConfigService } from '../core/services/user-config.service.js';

export class Desktop extends BaseComponent {
  private windowManager!: IWindowManager;
  private desktopService!: IDesktopService;
  private authService!: IAuthService;
  private userConfigService!: UserConfigService;

  private currentTime = new Date();
  private timeUpdateInterval?: number;

  /**
   * Mount the desktop component
   */
  async mount(): Promise<void> {
    console.log('Mounting Desktop component...');

    // Get required services
    this.windowManager = serviceContainer.get(SERVICE_TOKENS.WINDOW_MANAGER);
    this.desktopService = serviceContainer.get(SERVICE_TOKENS.DESKTOP_SERVICE);
    this.authService = serviceContainer.get(SERVICE_TOKENS.AUTH_SERVICE);
    this.userConfigService = serviceContainer.get(SERVICE_TOKENS.USER_CONFIG_SERVICE);

    // Build desktop structure
    this.buildDesktopStructure();

    // Setup event listeners
    this.setupEventListeners();

    // Start time updates
    this.startTimeUpdates();

    // Apply initial theme and OS style
    this.updateOSStyle();

    this._mounted = true;
    console.log('Desktop component mounted');
  }

  /**
   * Build the complete desktop structure
   */
  private buildDesktopStructure(): void {
    this.container.innerHTML = '';
    this.container.className = 'desktop-container';

    // Apply initial OS style class
    const initialOSStyle = this.desktopService.getOSStyle();
    this.container.classList.add(`os-${initialOSStyle}`);

    // Create menu bar
    this.createMenuBar();

    // Create windows container
    this.createWindowsContainer();

    // Create dock/taskbar based on OS style
    this.createDockTaskbar();

    // Create desktop icons area
    this.createDesktopIcons();
  }

  /**
   * Create menu bar (always visible at top)
   */
  private createMenuBar(): void {
    const menuBar = this.createElement('div', { class: 'menu-bar' });

    // App name
    const appName = this.createElement('div', { class: 'menu-app-name' }, 'VFS Desktop');
    this.addEventListener(appName, 'click', () => this.toggleFileExplorer());
    menuBar.appendChild(appName);

    // Menu items
    const menuItems = this.createElement('div', { class: 'menu-items' });
    menuBar.appendChild(menuItems);

    // Status icons
    const statusIcons = this.createElement('div', { class: 'menu-status-icons' });

    // Authentication buttons/user info
    this.createAuthSection(statusIcons);

    // Theme toggle
    const themeToggle = this.createElement('div', { class: 'menu-status-icon' });
    themeToggle.title = 'Toggle Theme';
    this.addEventListener(themeToggle, 'click', () => this.toggleTheme());
    statusIcons.appendChild(themeToggle);

    // OS style toggle
    const osToggle = this.createElement('div', { class: 'menu-status-icon' });
    osToggle.title = 'Switch OS Style';
    this.addEventListener(osToggle, 'click', () => this.toggleOSStyle());
    statusIcons.appendChild(osToggle);

    // Current time
    const timeDisplay = this.createElement('div', { 
      class: 'menu-status-icon',
      id: 'time-display'
    });
    this.updateTimeDisplay(timeDisplay);
    statusIcons.appendChild(timeDisplay);

    menuBar.appendChild(statusIcons);
    this.container.appendChild(menuBar);
  }

  /**
   * Create authentication section in menu bar
   */
  private createAuthSection(container: HTMLElement): void {
    const authSection = this.createElement('div', { class: 'auth-section' });
    
    if (this.authService.isAuthenticated()) {
      // Show user info
      const user = this.authService.getCurrentUser()!;
      const userButton = this.createElement('div', { class: 'menu-auth-button' });
      
      const userIcon = this.createElement('span', { class: 'material-icons' }, 'account_circle');
      const userName = this.createElement('span', {}, user.username);
      
      userButton.appendChild(userIcon);
      userButton.appendChild(userName);
      
      this.addEventListener(userButton, 'click', () => this.openUserProfile());
      authSection.appendChild(userButton);
    } else {
      // Show login/register buttons
      const loginButton = this.createElement('div', { class: 'menu-auth-button' });
      loginButton.innerHTML = `
        <span class="material-icons">login</span>
        <span>Login</span>
      `;
      this.addEventListener(loginButton, 'click', () => this.openLogin());
      authSection.appendChild(loginButton);

      const registerButton = this.createElement('div', { class: 'menu-auth-button' });
      registerButton.innerHTML = `
        <span class="material-icons">person_add</span>
        <span>Register</span>
      `;
      this.addEventListener(registerButton, 'click', () => this.openRegister());
      authSection.appendChild(registerButton);
    }

    container.appendChild(authSection);
  }

  /**
   * Create windows container
   */
  private createWindowsContainer(): void {
    const windowsContainer = this.createElement('div', { 
      class: 'windows-container',
      id: 'windows-container'
    });
    this.container.appendChild(windowsContainer);
  }

  /**
   * Create dock (macOS) or taskbar (Windows)
   */
  private createDockTaskbar(): void {
    const osStyle = this.desktopService.getOSStyle();
    
    if (osStyle === 'macos') {
      this.createDock();
    } else {
      this.createTaskbar();
    }
  }

  /**
   * Create macOS-style dock
   */
  private createDock(): void {
    const dockContainer = this.createElement('div', { class: 'dock-container' });
    const dock = this.createElement('div', { class: 'dock' });

    // Get docked applications - use all apps if getDockedApps returns empty
    let dockedApps = this.desktopService.getDockedApps();
    if (dockedApps.length === 0) {
      // Show main apps in dock
      dockedApps = this.desktopService.getApps().slice(0, 6);
    }

    for (const app of dockedApps) {
      const dockItem = this.createElement('div', { 
        class: 'dock-item',
        'data-app-id': app.id
      });

      const icon = this.createElement('span', { class: 'material-icons' }, app.icon);
      const tooltip = this.createElement('span', { class: 'dock-tooltip' }, app.name);

      dockItem.appendChild(icon);
      dockItem.appendChild(tooltip);

      // Add active indicator if app is running
      if (this.desktopService.isAppActive(app.id)) {
        const indicator = this.createElement('div', { class: 'dock-indicator' });
        dockItem.appendChild(indicator);
        dockItem.classList.add('active');
      }

      this.addEventListener(dockItem, 'click', () => this.toggleApp(app.id));
      dock.appendChild(dockItem);
    }

    dockContainer.appendChild(dock);
    this.container.appendChild(dockContainer);
  }

  /**
   * Create Windows-style taskbar
   */
  private createTaskbar(): void {
    const taskbar = this.createElement('div', { class: 'windows-taskbar' });

    // Start button
    const startButton = this.createElement('div', { class: 'taskbar-start' });
    startButton.innerHTML = `
      <span class="material-icons">apps</span>
      <span>Start</span>
    `;
    this.addEventListener(startButton, 'click', () => this.toggleStartMenu());
    taskbar.appendChild(startButton);

    // Running apps
    const taskbarApps = this.createElement('div', { class: 'taskbar-apps' });
    this.updateTaskbarApps(taskbarApps);
    taskbar.appendChild(taskbarApps);

    // System tray
    const systemTray = this.createElement('div', { class: 'taskbar-system' });
    
    const tray = this.createElement('div', { class: 'system-tray' });
    const osToggle = this.createElement('span', { 
      class: 'material-icons',
      title: 'Switch OS Style'
    }, 'desktop_windows');
    this.addEventListener(osToggle, 'click', () => this.toggleOSStyle());
    tray.appendChild(osToggle);
    systemTray.appendChild(tray);

    const clock = this.createElement('div', { 
      class: 'taskbar-clock',
      id: 'taskbar-time-display'
    });
    this.updateTimeDisplay(clock);
    systemTray.appendChild(clock);

    taskbar.appendChild(systemTray);
    this.container.appendChild(taskbar);
  }

  /**
   * Update taskbar apps (Windows style)
   */
  private updateTaskbarApps(container: HTMLElement): void {
    container.innerHTML = '';
    
    const runningWindows = this.windowManager.getVisibleWindows();
    
    for (const window of runningWindows) {
      const app = this.desktopService.getApp(window.id);
      if (!app) continue;

      const taskbarItem = this.createElement('div', { 
        class: 'taskbar-item active',
        title: app.name
      });

      const icon = this.createElement('span', { class: 'material-icons' }, app.icon);
      const name = this.createElement('span', { class: 'app-name' }, app.name);

      taskbarItem.appendChild(icon);
      taskbarItem.appendChild(name);

      this.addEventListener(taskbarItem, 'click', () => this.toggleApp(app.id));
      container.appendChild(taskbarItem);
    }
  }

  /**
   * Create desktop icons area
   */
  private createDesktopIcons(): void {
    const iconsArea = this.createElement('div', { class: 'desktop-icons' });

    // Add some default desktop icons
    this.addDesktopIcon(iconsArea, 'folder', 'Documents', () => this.openDocuments());
    this.addDesktopIcon(iconsArea, 'settings', 'Settings', () => this.openSettings());

    this.container.appendChild(iconsArea);
  }

  /**
   * Add a desktop icon
   */
  private addDesktopIcon(container: HTMLElement, icon: string, label: string, onClick: () => void): void {
    const iconElement = this.createElement('div', { class: 'desktop-icon' });
    
    const iconSpan = this.createElement('span', { class: 'icon material-icons' }, icon);
    const labelSpan = this.createElement('span', { class: 'icon-label' }, label);
    
    iconElement.appendChild(iconSpan);
    iconElement.appendChild(labelSpan);
    
    this.addEventListener(iconElement, 'click', onClick);
    this.addEventListener(iconElement, 'dblclick', onClick);
    
    container.appendChild(iconElement);
  }

  /**
   * Setup event listeners
   */
  private setupEventListeners(): void {
    // Listen for auth state changes
    eventBus.on('auth:login', () => this.updateAuthSection());
    eventBus.on('auth:logout', () => this.updateAuthSection());

    // Listen for window events
    eventBus.on('window:created', () => this.updateDockTaskbar());
    eventBus.on('window:closed', () => this.updateDockTaskbar());
    eventBus.on('window:activated', () => this.updateDockTaskbar());

    // Listen for OS style changes
    eventBus.on('desktop:os-style-changed', () => this.updateOSStyle());
    eventBus.on('desktop:theme-changed', () => this.updateThemeDisplay());
  }

  /**
   * Start time updates
   */
  private startTimeUpdates(): void {
    this.updateTime();
    this.timeUpdateInterval = window.setInterval(() => {
      this.updateTime();
    }, 60000); // Update every minute
  }

  /**
   * Update current time
   */
  private updateTime(): void {
    this.currentTime = new Date();
    
    const timeDisplay = document.getElementById('time-display');
    if (timeDisplay) {
      this.updateTimeDisplay(timeDisplay);
    }

    const taskbarTimeDisplay = document.getElementById('taskbar-time-display');
    if (taskbarTimeDisplay) {
      this.updateTimeDisplay(taskbarTimeDisplay);
    }
  }

  /**
   * Update time display element
   */
  private updateTimeDisplay(element: HTMLElement): void {
    const hours = this.currentTime.getHours();
    const minutes = this.currentTime.getMinutes();
    const ampm = hours >= 12 ? 'PM' : 'AM';
    const formattedHours = hours % 12 || 12;
    const formattedMinutes = minutes < 10 ? `0${minutes}` : minutes;
    
    element.textContent = `${formattedHours}:${formattedMinutes} ${ampm}`;
  }

  /**
   * Update OS style
   */
  private updateOSStyle(): void {
    const osStyle = this.desktopService.getOSStyle();
    
    // Remove existing OS classes
    this.container.classList.remove('os-macos', 'os-windows');
    this.container.classList.add(`os-${osStyle}`);

    // Rebuild dock/taskbar
    const existing = this.container.querySelector('.dock-container, .windows-taskbar');
    if (existing) {
      existing.remove();
    }
    this.createDockTaskbar();

    // Update OS toggle display
    this.updateOSToggleDisplay();
  }

  /**
   * Update OS toggle display
   */
  private updateOSToggleDisplay(): void {
    const osStyle = this.desktopService.getOSStyle();
    const toggles = this.container.querySelectorAll('[title="Switch OS Style"]');
    
    for (const toggle of toggles) {
      toggle.textContent = osStyle === 'macos' ? '' : '';
    }
  }

  /**
   * Update theme display
   */
  private updateThemeDisplay(): void {
    const theme = this.desktopService.getTheme();
    const toggles = this.container.querySelectorAll('[title="Toggle Theme"]');
    
    for (const toggle of toggles) {
      toggle.textContent = theme === 'dark' ? ' Dark' : ' Light';
    }
  }

  /**
   * Update authentication section
   */
  private updateAuthSection(): void {
    const authSection = this.container.querySelector('.auth-section');
    if (authSection) {
      authSection.remove();
    }
    
    const statusIcons = this.container.querySelector('.menu-status-icons');
    if (statusIcons && statusIcons instanceof HTMLElement) {
      this.createAuthSection(statusIcons);
    }
  }

  /**
   * Update dock/taskbar
   */
  private updateDockTaskbar(): void {
    const osStyle = this.desktopService.getOSStyle();
    
    if (osStyle === 'macos') {
      const dock = this.container.querySelector('.dock');
      if (dock) {
        // Update dock items
        this.updateDockItems(dock);
      }
    } else {
      const taskbarApps = this.container.querySelector('.taskbar-apps');
      if (taskbarApps) {
        this.updateTaskbarApps(taskbarApps as HTMLElement);
      }
    }
  }

  /**
   * Update dock items
   */
  private updateDockItems(dock: Element): void {
    const items = dock.querySelectorAll('.dock-item');
    for (const item of items) {
      const appId = item.getAttribute('data-app-id');
      if (appId) {
        const isActive = this.desktopService.isAppActive(appId);
        item.classList.toggle('active', isActive);
        
        const indicator = item.querySelector('.dock-indicator');
        if (isActive && !indicator) {
          const newIndicator = this.createElement('div', { class: 'dock-indicator' });
          item.appendChild(newIndicator);
        } else if (!isActive && indicator) {
          indicator.remove();
        }
      }
    }
  }

  // Event handlers
  private toggleFileExplorer(): void {
    this.toggleApp('file-explorer');
  }

  private toggleApp(appId: string): void {
    this.desktopService.launchApp(appId).catch(console.error);
  }

  private toggleTheme(): void {
    this.desktopService.toggleTheme();
  }

  private toggleOSStyle(): void {
    const current = this.desktopService.getOSStyle();
    const newStyle = current === 'macos' ? 'windows' : 'macos';
    this.desktopService.setOSStyle(newStyle);
  }

  private openLogin(): void {
    this.toggleApp('login');
  }

  private openRegister(): void {
    this.toggleApp('register');
  }

  private openUserProfile(): void {
    this.toggleApp('user-profile');
  }

  private openDocuments(): void {
    this.toggleApp('file-explorer');
  }

  private openSettings(): void {
    this.toggleApp('settings');
  }

  private toggleStartMenu(): void {
    // TODO: Implement Windows start menu
    console.log('Start menu clicked');
  }

  /**
   * Unmount the desktop component
   */
  override async unmount(): Promise<void> {
    // Stop time updates
    if (this.timeUpdateInterval) {
      clearInterval(this.timeUpdateInterval);
    }

    // Clean up event listeners
    this.cleanupEventListeners();

    await this.baseUnmount();
    console.log('Desktop component unmounted');
  }
}