/**
 * Base component class that all window components should extend
 * Provides common functionality and lifecycle management
 */

import type { IComponent } from '../../core/types/index.js';

export abstract class BaseComponent implements IComponent {
  protected _mounted = false;
  protected _destroyed = false;

  constructor(
    public readonly container: HTMLElement,
    public inputs?: Record<string, unknown>
  ) {
    this.bindMethods();
  }

  /**
   * Bind all methods to this instance to avoid context issues
   */
  private bindMethods(): void {
    const methodNames = Object.getOwnPropertyNames(Object.getPrototypeOf(this));
    for (const methodName of methodNames) {
      const method = (this as any)[methodName];
      if (typeof method === 'function' && methodName !== 'constructor') {
        (this as any)[methodName] = method.bind(this);
      }
    }
  }

  /**
   * Mount the component - must be implemented by subclasses
   */
  abstract mount(): void | Promise<void>;

  /**
   * Unmount the component - should be overridden if cleanup is needed
   */
  unmount(): void | Promise<void> {
    this._mounted = false;
    this._destroyed = true;
    this.container.innerHTML = '';
  }

  /**
   * Update component inputs - can be overridden for reactive updates
   */
  update(inputs: Record<string, unknown>): void | Promise<void> {
    this.inputs = { ...this.inputs, ...inputs };
  }

  /**
   * Check if component is mounted
   */
  get isMounted(): boolean {
    return this._mounted;
  }

  /**
   * Check if component is destroyed
   */
  get isDestroyed(): boolean {
    return this._destroyed;
  }

  /**
   * Create an element with optional attributes and content
   */
  protected createElement<K extends keyof HTMLElementTagNameMap>(
    tagName: K,
    attributes?: Record<string, string>,
    content?: string
  ): HTMLElementTagNameMap[K] {
    const element = document.createElement(tagName);
    
    if (attributes) {
      for (const [key, value] of Object.entries(attributes)) {
        element.setAttribute(key, value);
      }
    }
    
    if (content) {
      element.textContent = content;
    }
    
    return element;
  }

  /**
   * Add event listener with automatic cleanup
   */
  protected addEventListener<K extends keyof HTMLElementEventMap>(
    element: HTMLElement,
    type: K,
    listener: (event: HTMLElementEventMap[K]) => void,
    options?: boolean | AddEventListenerOptions
  ): void {
    const eventListener = listener as EventListener;
    element.addEventListener(type, eventListener, options);
    
    // Store for cleanup on unmount
    if (!this._eventListeners) {
      this._eventListeners = [];
    }
    this._eventListeners.push({ element, type, listener: eventListener, options });
  }

  private _eventListeners?: Array<{
    element: HTMLElement;
    type: string;
    listener: EventListener;
    options?: boolean | AddEventListenerOptions | undefined;
  }>;

  /**
   * Clean up event listeners
   */
  protected cleanupEventListeners(): void {
    if (this._eventListeners) {
      for (const { element, type, listener, options } of this._eventListeners) {
        element.removeEventListener(type, listener, options);
      }
      this._eventListeners = [];
    }
  }

  /**
   * Override unmount to include event cleanup
   */
  protected async baseUnmount(): Promise<void> {
    this.cleanupEventListeners();
    this._mounted = false;
    this._destroyed = true;
  }

  /**
   * Set CSS custom properties on the container
   */
  protected setCSSProperty(property: string, value: string): void {
    this.container.style.setProperty(property, value);
  }

  /**
   * Get CSS custom property value
   */
  protected getCSSProperty(property: string): string {
    return getComputedStyle(this.container).getPropertyValue(property);
  }

  /**
   * Add CSS classes to container
   */
  protected addClass(...classes: string[]): void {
    this.container.classList.add(...classes);
  }

  /**
   * Remove CSS classes from container
   */
  protected removeClass(...classes: string[]): void {
    this.container.classList.remove(...classes);
  }

  /**
   * Toggle CSS classes on container
   */
  protected toggleClass(className: string, force?: boolean): void {
    this.container.classList.toggle(className, force);
  }

  /**
   * Query selector within the component
   */
  protected querySelector<T extends Element = Element>(selector: string): T | null {
    return this.container.querySelector<T>(selector);
  }

  /**
   * Query all elements within the component
   */
  protected querySelectorAll<T extends Element = Element>(selector: string): NodeListOf<T> {
    return this.container.querySelectorAll<T>(selector);
  }

  /**
   * Create and append child element
   */
  protected appendElement<K extends keyof HTMLElementTagNameMap>(
    tagName: K,
    attributes?: Record<string, string>,
    content?: string,
    parent?: HTMLElement
  ): HTMLElementTagNameMap[K] {
    const element = this.createElement(tagName, attributes, content);
    (parent || this.container).appendChild(element);
    return element;
  }

  /**
   * Emit a custom event
   */
  protected emit(eventName: string, detail?: unknown): void {
    const event = new CustomEvent(eventName, { detail });
    this.container.dispatchEvent(event);
  }

  /**
   * Get input value with type safety
   */
  protected getInput<T = unknown>(key: string, defaultValue: T): T {
    return (this.inputs?.[key] as T) ?? defaultValue;
  }

  /**
   * Set input value
   */
  protected setInput<T = unknown>(key: string, value: T): void {
    if (!this.inputs) {
      this.inputs = {};
    }
    this.inputs[key] = value;
  }
}