/**
 * Component Scanner
 * Automatically discovers and registers components based on file conventions and metadata
 */

import type { ComponentMetadata, ComponentModule } from './component-metadata.js';
import { componentRegistry } from './component-registry.js';

/**
 * Component discovery configuration
 */
export interface ComponentScanConfig {
  /** Base paths to scan for components */
  basePaths: string[];
  /** File patterns to match (glob patterns) */
  patterns: string[];
  /** File patterns to exclude */
  excludePatterns?: string[];
  /** Whether to scan recursively */
  recursive?: boolean;
  /** Module definitions for grouping components */
  modules?: ComponentModule[];
}

/**
 * Component file convention
 * Components should follow naming conventions for auto-discovery
 */
export interface ComponentFileConvention {
  /** File must end with .component.ts */
  suffix: '.component.ts';
  /** Export name should match filename (PascalCase) */
  exportName: string;
  /** Metadata export name */
  metadataExport: 'metadata' | 'componentMetadata';
}

/**
 * Component Scanner
 * Discovers components automatically and registers them
 */
export class ComponentScanner {
  private config: ComponentScanConfig;
  
  constructor(config: ComponentScanConfig) {
    this.config = config;
  }

  /**
   * Scan and register all components
   */
  async scanAndRegister(): Promise<{
    componentsFound: number;
    componentsRegistered: number;
    modulesRegistered: number;
    errors: string[];
  }> {
    const result = {
      componentsFound: 0,
      componentsRegistered: 0,
      modulesRegistered: 0,
      errors: [] as string[]
    };

    try {
      // Register modules first
      if (this.config.modules) {
        for (const module of this.config.modules) {
          await this.registerModule(module);
          result.modulesRegistered++;
        }
      }

      // Scan for components using Vite's dynamic imports
      await this.scanWithViteGlob(result);
      
    } catch (error) {
      result.errors.push(`Scanner error: ${error instanceof Error ? error.message : String(error)}`);
    }

    return result;
  }

  /**
   * Use Vite's glob import feature for component discovery
   */
  private async scanWithViteGlob(result: {
    componentsFound: number;
    componentsRegistered: number;
    errors: string[];
  }): Promise<void> {
    // Since we can't dynamically scan the filesystem in the browser,
    // we'll create a registration system that components can use to self-register
    
    // Components will call ComponentScanner.registerComponentFile() in their modules
    // This is a compromise between auto-discovery and manual registration
    
    console.log('Component scanner initialized. Components should self-register using registerComponentFile()');
  }

  /**
   * Register a module with its components
   */
  private async registerModule(module: ComponentModule): Promise<void> {
    try {
      // Register module components with their loaders
      const componentRegistrations: Array<{
        metadata: ComponentMetadata;
        loader: () => Promise<any>;
      }> = [];

      for (const componentId of module.components) {
        // Generate metadata for module components
        const metadata: ComponentMetadata = {
          id: componentId,
          name: this.generateDisplayName(componentId),
          category: module.category || 'uncategorized',
          ...(module.version && { version: module.version }),
          defaultWindow: {
            title: this.generateDisplayName(componentId),
            position: { x: 100, y: 100 },
            size: { width: 800, height: 600 },
            minSize: { width: 400, height: 300 },
            isResizable: true,
            isDraggable: true
          }
        };

        // Create loader function
        const loader = this.createComponentLoader(componentId, module.id);
        
        componentRegistrations.push({ metadata, loader });
      }

      componentRegistry.registerModule(module, componentRegistrations);
      
    } catch (error) {
      throw new Error(`Failed to register module ${module.id}: ${error instanceof Error ? error.message : String(error)}`);
    }
  }

  /**
   * Create a loader function for a component
   */
  private createComponentLoader(componentId: string, moduleId: string) {
    return async () => {
      // This will be replaced with actual dynamic imports in the specific scanner implementations
      throw new Error(`Component loader not implemented for ${componentId} in module ${moduleId}`);
    };
  }

  /**
   * Generate display name from component ID
   */
  private generateDisplayName(componentId: string): string {
    return componentId
      .split('-')
      .map(word => word.charAt(0).toUpperCase() + word.slice(1))
      .join(' ');
  }

  /**
   * Static method for components to self-register
   * This is called by individual component modules
   */
  static registerComponentFile(
    componentId: string,
    metadata: Partial<ComponentMetadata>,
    loader: () => Promise<any>
  ): void {
    const fullMetadata: ComponentMetadata = {
      id: componentId,
      name: metadata.name || componentId,
      category: metadata.category || 'uncategorized',
      ...metadata
    };

    componentRegistry.registerComponent(fullMetadata, loader);
  }
}

/**
 * Create component scanner with default configuration
 */
export function createComponentScanner(config?: Partial<ComponentScanConfig>): ComponentScanner {
  const defaultConfig: ComponentScanConfig = {
    basePaths: ['./src/components'],
    patterns: ['**/*.component.ts'],
    excludePatterns: ['**/*.spec.ts', '**/*.test.ts'],
    recursive: true,
    ...config
  };

  return new ComponentScanner(defaultConfig);
}

/**
 * Auto-discovery decorator for components
 * Components can use this to automatically register themselves
 */
export function AutoDiscover(metadata: Partial<ComponentMetadata>) {
  return function<T extends new (...args: any[]) => any>(constructor: T) {
    // Extract component ID from constructor name or metadata
    const componentId = metadata.id || constructor.name.replace(/Component$/, '').toLowerCase();
    
    // Self-register the component
    ComponentScanner.registerComponentFile(
      componentId,
      {
        ...metadata,
        id: componentId
      },
      async () => constructor
    );

    return constructor;
  };
}