/**
 * Dependency Injection Container for VFS Desktop
 * Provides service registration and resolution with lifecycle management
 */

import type { IService, IServiceContainer, ServiceConstructor } from '../types/index.js';

interface ServiceRegistration<T extends IService = IService> {
  constructor?: ServiceConstructor<T>;
  instance?: T | undefined;
  singleton: boolean;
  initialized: boolean;
}

export class ServiceContainer implements IServiceContainer {
  private services = new Map<string, ServiceRegistration>();
  private instances = new Map<string, IService>();

  /**
   * Register a service constructor
   */
  register<T extends IService>(
    token: string, 
    constructor: ServiceConstructor<T>,
    singleton = true
  ): void {
    this.services.set(token, {
      constructor,
      singleton,
      initialized: false
    });
  }

  /**
   * Register a service instance
   */
  registerInstance<T extends IService>(token: string, instance: T): void {
    this.services.set(token, {
      instance,
      singleton: true,
      initialized: true
    } as ServiceRegistration<T>);
    this.instances.set(token, instance);
  }

  /**
   * Get a service instance
   */
  get<T extends IService>(token: string): T {
    const registration = this.services.get(token);
    if (!registration) {
      throw new Error(`Service not registered: ${token}`);
    }

    // Return existing instance for singletons
    if (registration.singleton && registration.instance) {
      return registration.instance as T;
    }

    // Return cached instance
    const cachedInstance = this.instances.get(token);
    if (cachedInstance) {
      return cachedInstance as T;
    }

    // Create new instance
    if (!registration.constructor) {
      throw new Error(`Service constructor not available: ${token}`);
    }

    const instance = new registration.constructor() as T;

    // Cache singleton instances
    if (registration.singleton) {
      registration.instance = instance;
      this.instances.set(token, instance);
    }

    return instance;
  }

  /**
   * Check if a service is registered
   */
  has(token: string): boolean {
    return this.services.has(token);
  }

  /**
   * Initialize all registered services
   */
  async initializeAll(): Promise<void> {
    const initPromises: Promise<void>[] = [];

    for (const [token, registration] of this.services) {
      if (!registration.initialized) {
        const initPromise = this.initializeService(token);
        initPromises.push(initPromise);
      }
    }

    await Promise.all(initPromises);
  }

  /**
   * Initialize a specific service
   */
  private async initializeService(token: string): Promise<void> {
    const registration = this.services.get(token);
    if (!registration || registration.initialized) {
      return;
    }

    try {
      const instance = this.get(token);
      if (instance.initialize) {
        await instance.initialize();
      }
      registration.initialized = true;
    } catch (error) {
      console.error(`Failed to initialize service ${token}:`, error);
      throw error;
    }
  }

  /**
   * Destroy all services
   */
  async destroyAll(): Promise<void> {
    const destroyPromises: Promise<void>[] = [];

    for (const instance of this.instances.values()) {
      if (instance.destroy) {
        const result = instance.destroy();
        if (result instanceof Promise) {
          destroyPromises.push(result);
        }
      }
    }

    await Promise.all(destroyPromises);
    this.instances.clear();
    
    // Reset initialization state
    for (const registration of this.services.values()) {
      registration.initialized = false;
      if (registration.singleton) {
        registration.instance = undefined;
      }
    }
  }

  /**
   * Get all registered service tokens
   */
  getRegisteredServices(): string[] {
    return Array.from(this.services.keys());
  }

  /**
   * Clear all registrations
   */
  clear(): void {
    this.services.clear();
    this.instances.clear();
  }
}

// Global service container instance
export const serviceContainer = new ServiceContainer();

// Service tokens (constants to avoid typos)
export const SERVICE_TOKENS = {
  WINDOW_MANAGER: 'WindowManager',
  DESKTOP_SERVICE: 'DesktopService',
  AUTH_SERVICE: 'AuthService',
  FILE_SERVICE: 'FileService',
  COMPONENT_REGISTRY: 'ComponentRegistry',
  COMPONENT_LOADER: 'ComponentLoaderService',
  USER_CONFIG_SERVICE: 'UserConfigService',
  THEME_SERVICE: 'ThemeService',
  ANIMATION_SERVICE: 'AnimationService'
} as const;