/**
 * Component Loader Service
 * Manages dynamic loading and instantiation of window components
 */

import type { BaseComponent } from '../../components/base/base-component.js';
import type { IComponent, IService } from '../types/index.js';

export interface ComponentConstructor {
  new(container: HTMLElement, inputs?: Record<string, unknown>, ...args: any[]): IComponent;
}

export interface WebComponentConstructor {
  new(): HTMLElement;
}

export type AnyComponentConstructor = ComponentConstructor | WebComponentConstructor;

export interface ComponentRegistry {
  [key: string]: () => Promise<AnyComponentConstructor>;
}

export class ComponentLoaderService implements IService {
  readonly name = 'ComponentLoaderService';

  private registry: ComponentRegistry = {};

  /**
   * Initialize the component loader
   */
  async initialize(): Promise<void> {
    console.log('Initializing Component Loader Service...');
    
    // Register default components
    this.registerDefaultComponents();
    
    console.log('Component Loader Service initialized');
  }

  /**
   * Register default components
   */
  private registerDefaultComponents(): void {
    // Auth components
    this.registerComponent('login', () => 
      import('../../components/windows/login.component.js')
        .then(m => m.LoginComponent)
    );

    this.registerComponent('register', () => 
      import('../../components/windows/register.component.js')
        .then(m => m.RegisterComponent)
    );

    // File Explorer component (KISS implementation)
    this.registerComponent('file-explorer', () =>
      import('../../components/windows/file-explorer.component.js')
        .then(m => m.FileExplorerComponent)
    );

    // Test Web Component
    this.registerComponent('test-web-component', () =>
      import('../../components/windows/test-web-component.js')
        .then(m => m.TestWebComponent)
    );

    // Todo App Web Components
    this.registerComponent('todo-app', () =>
      import('../../components/windows/todo-app.component.js')
        .then(m => m.TodoAppComponent)
    );

    this.registerComponent('add-todo', () =>
      import('../../components/windows/add-todo.component.js')
        .then(m => m.AddTodoComponent)
    );

    this.registerComponent('todo-item', () =>
      import('../../components/windows/todo-item.component.js')
        .then(m => m.TodoItemComponent)
    );

    // TODO: Register other components as they are created
  }

  /**
   * Register a component
   */
  registerComponent(name: string, loader: () => Promise<AnyComponentConstructor>): void {
    this.registry[name] = loader;
    console.log(`Component '${name}' registered`);
  }

  /**
   * Load and instantiate a component
   */
  async loadComponent(
    name: string, 
    container: HTMLElement, 
    inputs?: Record<string, unknown>
  ): Promise<IComponent | null> {
    const loader = this.registry[name];
    if (!loader) {
      console.warn(`Component '${name}' not found in registry`);
      return null;
    }

    try {
      console.log(`Loading component '${name}'...`);
      const ComponentClass = await loader();

      // Check if it's a Web Component (extends HTMLElement)
      if (this.isWebComponent(ComponentClass)) {
        return this.createWebComponentAdapter(ComponentClass, container, inputs);
      } else {
        // Legacy BaseComponent
        const instance = new (ComponentClass as ComponentConstructor)(container, inputs);
        console.log(`Component '${name}' loaded successfully`);
        return instance;
      }
    } catch (error) {
      console.error(`Failed to load component '${name}':`, error);
      return null;
    }
  }

  /**
   * Check if a component class is a Web Component
   */
  private isWebComponent(ComponentClass: AnyComponentConstructor): ComponentClass is WebComponentConstructor {
    return ComponentClass.prototype instanceof HTMLElement;
  }

  /**
   * Create an adapter for Web Components to work with the component system
   */
  private createWebComponentAdapter(
    WebComponentClass: WebComponentConstructor,
    container: HTMLElement,
    inputs?: Record<string, unknown>
  ): IComponent {
    let mountedWebComponent: HTMLElement | null = null;

    return {
      container,
      inputs,

      mount: (): void => {
        // Create and append the Web Component
        const webComponent = new WebComponentClass();
        mountedWebComponent = webComponent;

        // Set inputs as attributes or properties if needed
        if (inputs) {
          Object.entries(inputs).forEach(([key, value]) => {
            if (typeof value === 'string') {
              webComponent.setAttribute(key, value);
            } else if (typeof value === 'boolean') {
              if (value) {
                webComponent.setAttribute(key, '');
              } else {
                webComponent.removeAttribute(key);
              }
            } else {
              (webComponent as any)[key] = value;
            }
          });
        }

        container.appendChild(webComponent);
      },

      unmount: (): void => {
        // Remove the mounted Web Component
        if (mountedWebComponent && container.contains(mountedWebComponent)) {
          container.removeChild(mountedWebComponent);
          mountedWebComponent = null;
        }
      },

      update: (newInputs: Record<string, unknown>): void => {
        // Update inputs on the mounted Web Component
        if (mountedWebComponent && mountedWebComponent instanceof WebComponentClass) {
          Object.entries(newInputs).forEach(([key, value]) => {
            if (typeof value === 'string') {
              mountedWebComponent!.setAttribute(key, value);
            } else if (typeof value === 'boolean') {
              if (value) {
                mountedWebComponent!.setAttribute(key, '');
              } else {
                mountedWebComponent!.removeAttribute(key);
              }
            } else {
              (mountedWebComponent as any)[key] = value;
            }
          });
        }
      }
    };
  }

  /**
   * Check if component is registered
   */
  hasComponent(name: string): boolean {
    return name in this.registry;
  }

  /**
   * Get list of registered component names
   */
  getRegisteredComponents(): string[] {
    return Object.keys(this.registry);
  }

  /**
   * Dispose of the service
   */
  async dispose(): Promise<void> {
    this.registry = {};
    console.log('Component Loader Service disposed');
  }
}