/**
 * Component Auto-Discovery and Registration
 * Registers all component modules and sets up the discovery system
 */

import { createComponentScanner, ComponentScanner } from '../core/component-system/component-scanner.js';
import { componentRegistry } from '../core/component-system/component-registry.js';
import { allModules } from './modules.js';
import { eventBus } from '../core/events/event-bus.js';
import { serviceContainer, SERVICE_TOKENS } from '../core/di/service-container.js';
import type { IWindowManager, WindowRegistryEntry } from '../core/types/index.js';

/**
 * Component discovery and registration service
 */
export class ComponentDiscoveryService {
  private static instance: ComponentDiscoveryService;
  private scanner: ComponentScanner;
  private isInitialized = false;
    private windowManagerRegistered = false;
  
  private constructor() {
    this.scanner = createComponentScanner({
      basePaths: ['./src/components'],
      patterns: ['**/*.component.ts'],
      modules: allModules
    });
  }
  
  static getInstance(): ComponentDiscoveryService {
    if (!ComponentDiscoveryService.instance) {
      ComponentDiscoveryService.instance = new ComponentDiscoveryService();
    }
    return ComponentDiscoveryService.instance;
  }

  /**
   * Initialize the component discovery system
   */
  async initialize(): Promise<void> {
    if (this.isInitialized) {
      return;
    }

    console.log(' Initializing component discovery system...');
    
    try {
      // Register all modules and their components
      const result = await this.scanner.scanAndRegister();
      
      console.log(` Component discovery completed:
        - Modules registered: ${result.modulesRegistered}
        - Components found: ${result.componentsFound}
        - Components registered: ${result.componentsRegistered}
        ${result.errors.length > 0 ? `- Errors: ${result.errors.length}` : ''}
      `);
      
      if (result.errors.length > 0) {
        console.warn('Discovery errors:', result.errors);
      }
      
      // Register specific component loaders
      this.registerComponentLoaders();
      
      // Preload high-priority components
      await this.preloadCriticalComponents();
      
      this.isInitialized = true;
      
      eventBus.emit('component-discovery:initialized', {
        modulesCount: result.modulesRegistered,
        componentsCount: result.componentsRegistered
      });

        // Listen for service container completion to retry window manager registration
        eventBus.on('services:initialized', () => {
            this.retryWindowManagerRegistration();
        });

    } catch (error) {
      console.error('Failed to initialize component discovery:', error);
      throw error;
    }
  }

  /**
   * Register component loaders for existing components
   */
  private registerComponentLoaders(): void {
    // Register existing components that are already created
    this.registerExistingComponents();
    
    // Set up pattern for future components to self-register
    this.setupSelfRegistration();
    
    // Register components with window manager
    this.registerComponentsWithWindowManager();
  }

  /**
   * Register components that already exist
   */
  private registerExistingComponents(): void {
    // Auth components (already implemented)
    ComponentScanner.registerComponentFile(
      'login',
      {
        id: 'login',
        name: 'Login',
        category: 'auth',
        description: 'User login component',
        icon: 'login',
        defaultWindow: {
          title: 'Login',
          position: { x: 400, y: 200 },
          size: { width: 400, height: 500 },
          minSize: { width: 350, height: 400 },
          isResizable: false,
          isDraggable: true
        },
        allowMultipleInstances: false,
        loadPriority: 1
      },
      async () => {
        const { LoginComponent } = await import('../components/windows/login.component.js');
        return LoginComponent;
      }
    );

    ComponentScanner.registerComponentFile(
      'register',
      {
        id: 'register',
        name: 'Register',
        category: 'auth',
        description: 'User registration component',
        icon: 'person_add',
        defaultWindow: {
          title: 'Create Account',
          position: { x: 450, y: 150 },
          size: { width: 500, height: 600 },
          minSize: { width: 400, height: 500 },
          isResizable: false,
          isDraggable: true
        },
        allowMultipleInstances: false,
        loadPriority: 2
      },
      async () => {
        const { RegisterComponent } = await import('../components/windows/register.component.js');
        return RegisterComponent;
      }
    );

      // File Explorer component (KISS implementation)
      ComponentScanner.registerComponentFile(
          'file-explorer',
          {
              id: 'file-explorer',
              name: 'File Explorer',
              category: 'file-management',
              description: 'KISS Virtual File System Explorer - simplified but better in every way',
              icon: '',
              defaultWindow: {
                  title: 'File Explorer',
                  position: { x: 200, y: 100 },
                  size: { width: 800, height: 600 },
                  minSize: { width: 400, height: 300 },
                  isResizable: true,
                  isDraggable: true
              },
              allowMultipleInstances: true,
              loadPriority: 3,
              capabilities: [
                  {
                      id: 'virtual-file-system',
                      name: 'Virtual File System',
                      description: 'Navigate and manage virtual file system'
                  },
                  {
                      id: 'drag-drop',
                      name: 'Drag & Drop',
                      description: 'Support for drag and drop file operations'
                  },
                  {
                      id: 'context-menu',
                      name: 'Context Menu',
                      description: 'Right-click context menu for file operations'
                  },
                  {
                      id: 'keyboard-shortcuts',
                      name: 'Keyboard Shortcuts',
                      description: 'Full keyboard navigation support'
                  }
              ],
              tags: ['files', 'explorer', 'vfs', 'navigation', 'kiss']
          },
          async () => {
              const { FileExplorerComponent } = await import('../components/windows/file-explorer.component.js');
              return FileExplorerComponent;
          }
      );
  }

  /**
   * Set up pattern for future components to self-register
   */
  private setupSelfRegistration(): void {
    // Components will use the @AutoDiscover decorator or call ComponentScanner.registerComponentFile
    // This provides a clean pattern for adding new components
    
    // Example for future components:
    // ComponentScanner.registerComponentFile('component-id', metadata, loader);
  }

  /**
   * Preload critical components for better performance
   */
  private async preloadCriticalComponents(): Promise<void> {
    console.log(' Preloading critical components...');
    
    try {
      // Preload auth components and desktop essentials
      await componentRegistry.preloadComponents({
        categories: ['auth', 'desktop'],
        maxPriority: 3,
        maxCount: 10
      });
      
      console.log(' Critical components preloaded');
    } catch (error) {
      console.warn('Failed to preload some components:', error);
    }
  }

  /**
   * Register components with window manager for proper window handling
   */
  private registerComponentsWithWindowManager(): void {
      if (this.windowManagerRegistered) {
          console.log('Components already registered with window manager, skipping');
          return;
      }

    try {
      const windowManager = serviceContainer.get<any>(SERVICE_TOKENS.WINDOW_MANAGER); // Use any to access registerWindow
      
      // Get all registered components and register them with window manager
      const allComponents = componentRegistry.getAllComponents();
        let registeredCount = 0;
      
      for (const component of allComponents) {
        if (component.defaultWindow) {
          const windowConfig: WindowRegistryEntry = {
            defaultTitle: component.defaultWindow.title || component.name,
            defaultSize: component.defaultWindow.size || { width: 800, height: 600 },
            defaultPosition: component.defaultWindow.position || { x: 100, y: 100 },
            minSize: component.defaultWindow.minSize || { width: 400, height: 300 },
            ...(component.defaultWindow.maxSize && { maxSize: component.defaultWindow.maxSize }),
            category: component.category,
            isDocked: false,
            isResizable: component.defaultWindow.isResizable ?? true,
            isDraggable: component.defaultWindow.isDraggable ?? true
          };
          
          if (typeof windowManager.registerWindow === 'function') {
            windowManager.registerWindow(component.id, windowConfig);
              registeredCount++;
          }
        }
      }
      
        this.windowManagerRegistered = true;
        console.log(` Registered ${registeredCount} components with window manager`);
    } catch (error) {
        console.warn('Failed to register components with window manager (will retry after services are initialized):', error);
    }
    }

    /**
     * Retry window manager registration after services are initialized
     */
    private retryWindowManagerRegistration(): void {
        console.log(' Retrying component registration with window manager...');
        this.registerComponentsWithWindowManager();
  }

  /**
   * Get discovery statistics
   */
  getStatistics() {
    const registryStats = componentRegistry.getLoadStatistics();
    const modules = componentRegistry.getAllModules();
    
    return {
      ...registryStats,
      modules: modules.length,
      isInitialized: this.isInitialized
    };
  }

  /**
   * Register a new component at runtime
   */
  registerComponent(
    componentId: string,
    metadata: any,
    loader: () => Promise<any>
  ): void {
    ComponentScanner.registerComponentFile(componentId, metadata, loader);
  }

  /**
   * Load a component by ID
   */
  async loadComponent(componentId: string) {
    return componentRegistry.loadComponent(componentId);
  }

  /**
   * Search for components
   */
  searchComponents(query: string) {
    return componentRegistry.searchComponents(query);
  }

  /**
   * Get components by category
   */
  getComponentsByCategory(category: string) {
    return componentRegistry.getComponentsByCategory(category);
  }
}

// Export singleton instance
export const componentDiscovery = ComponentDiscoveryService.getInstance();