/**
 * Theme Service
 * Manages application theming and visual appearance
 */

import type { IService, Theme } from '../core/types/index.js';
import { eventBus } from '../core/events/event-bus.js';

export class ThemeService implements IService {
  readonly name = 'ThemeService';
  
  private currentTheme: Theme = 'light';
  private systemPreference: Theme = 'light';

  /**
   * Initialize the theme service
   */
  async initialize(): Promise<void> {
    console.log('Initializing Theme Service...');
    
    // Detect system theme preference
    this.detectSystemTheme();
    
    // Load saved theme or use system preference
    this.loadTheme();
    
    // Listen for system theme changes
    this.setupSystemThemeListener();
    
    console.log('Theme Service initialized');
  }

  /**
   * Detect system theme preference
   */
  private detectSystemTheme(): void {
    if (window.matchMedia && window.matchMedia('(prefers-color-scheme: dark)').matches) {
      this.systemPreference = 'dark';
    } else {
      this.systemPreference = 'light';
    }
  }

  /**
   * Load theme from localStorage or use system preference
   */
  private loadTheme(): void {
    try {
      const savedTheme = localStorage.getItem('vfs-theme') as Theme;
      if (savedTheme === 'light' || savedTheme === 'dark') {
        this.setTheme(savedTheme);
      } else {
        this.setTheme(this.systemPreference);
      }
    } catch (error) {
      console.warn('Failed to load theme preference:', error);
      this.setTheme(this.systemPreference);
    }
  }

  /**
   * Setup listener for system theme changes
   */
  private setupSystemThemeListener(): void {
    if (window.matchMedia) {
      const mediaQuery = window.matchMedia('(prefers-color-scheme: dark)');
      mediaQuery.addListener((e) => {
        this.systemPreference = e.matches ? 'dark' : 'light';
        
        // If user hasn't set a custom theme, follow system
        const savedTheme = localStorage.getItem('vfs-theme');
        if (!savedTheme) {
          this.setTheme(this.systemPreference);
        }
      });
    }
  }

  /**
   * Get current theme
   */
  getTheme(): Theme {
    return this.currentTheme;
  }

  /**
   * Set theme
   */
  setTheme(theme: Theme): void {
    if (this.currentTheme === theme) return;

    this.currentTheme = theme;
    
    // Apply theme to document
    document.documentElement.setAttribute('data-theme', theme);
    
    // Save to localStorage
    try {
      localStorage.setItem('vfs-theme', theme);
    } catch (error) {
      console.warn('Failed to save theme preference:', error);
    }
    
    // Emit theme change event
    eventBus.emit('desktop:theme-changed', theme);
    
    console.log(`Theme set to: ${theme}`);
  }

  /**
   * Toggle between light and dark theme
   */
  toggleTheme(): void {
    const newTheme = this.currentTheme === 'light' ? 'dark' : 'light';
    this.setTheme(newTheme);
  }

  /**
   * Get system theme preference
   */
  getSystemPreference(): Theme {
    return this.systemPreference;
  }

  /**
   * Use system theme preference
   */
  useSystemTheme(): void {
    this.setTheme(this.systemPreference);
  }

  /**
   * Check if currently using system theme
   */
  isUsingSystemTheme(): boolean {
    return this.currentTheme === this.systemPreference;
  }

  /**
   * Get available themes
   */
  getAvailableThemes(): Theme[] {
    return ['light', 'dark'];
  }

  /**
   * Apply theme to specific element
   */
  applyThemeToElement(element: HTMLElement, theme?: Theme): void {
    const targetTheme = theme || this.currentTheme;
    element.setAttribute('data-theme', targetTheme);
  }

  /**
   * Remove theme from specific element (inherit from parent)
   */
  removeThemeFromElement(element: HTMLElement): void {
    element.removeAttribute('data-theme');
  }

  /**
   * Get CSS custom property value for current theme
   */
  getCSSProperty(property: string): string {
    return getComputedStyle(document.documentElement).getPropertyValue(property);
  }

  /**
   * Set CSS custom property
   */
  setCSSProperty(property: string, value: string): void {
    document.documentElement.style.setProperty(property, value);
  }

  /**
   * Create a themed color palette
   */
  getColorPalette(): Record<string, string> {
    return {
      background: this.getCSSProperty('--bg'),
      backgroundSecondary: this.getCSSProperty('--bg-secondary'),
      text: this.getCSSProperty('--text'),
      textSecondary: this.getCSSProperty('--text-secondary'),
      accent: this.getCSSProperty('--accent'),
      border: this.getCSSProperty('--border'),
      success: this.getCSSProperty('--success'),
      warning: this.getCSSProperty('--warning'),
      danger: this.getCSSProperty('--danger')
    };
  }

  /**
   * Check if dark theme is active
   */
  isDarkTheme(): boolean {
    return this.currentTheme === 'dark';
  }

  /**
   * Check if light theme is active
   */
  isLightTheme(): boolean {
    return this.currentTheme === 'light';
  }

  /**
   * Destroy the theme service
   */
  async destroy(): Promise<void> {
    // Reset to light theme
    document.documentElement.removeAttribute('data-theme');
    console.log('Theme Service destroyed');
  }
}