/**
 * User Configuration Service
 * Handles saving and retrieving user preferences and settings
 * Based on Angular client's UserConfigService
 */

import type { IService } from '../types/index.js';
import { serviceContainer, SERVICE_TOKENS } from '../di/service-container.js';
import { eventBus } from '../events/event-bus.js';
import type { IAuthService } from '../types/index.js';

export interface UserConfig {
  userId: string;
  settings: Record<string, any>;
}

export interface WindowPosition {
  x: number;
  y: number;
}

export interface WindowSize {
  width: number;
  height: number;
}

export interface SavedWindow {
  id: string;
  title?: string;
  icon?: string;
  position: WindowPosition;
  size: WindowSize;
  minSize?: WindowSize;
  zIndex?: number;
  isMinimized?: boolean;
  isFullscreen?: boolean;
  componentInputs?: Record<string, any>;
  isTack?: boolean;
}

export class UserConfigService implements IService {
  readonly name = 'UserConfigService';

  private authService!: IAuthService;
  private config: UserConfig | null = null;
  private apiBaseUrl = '/api';
  private readonly apiUrl = '/user/config';
  
  // Save status tracking
  private lastSaveAttempt: Date | null = null;
  private lastSaveSuccess: Date | null = null;
  private lastSaveError: string | null = null;
  private isDebugMode = true;

  // Debouncing for saves to prevent rate limiting
  private saveTimeout: number | null = null;
  private readonly saveDebounceMs = 2000; // Wait 2 seconds after last save request

  /**
   * Initialize the user config service
   */
  async initialize(): Promise<void> {
    console.log('Initializing User Config Service...');
    
    // Get auth service
    this.authService = serviceContainer.get(SERVICE_TOKENS.AUTH_SERVICE);
    
    // Listen for auth events
    eventBus.on('auth:login', () => this.fetchConfig());
    eventBus.on('auth:logout', () => this.clearConfig());
    
    // If already authenticated, fetch config
    if (this.authService.isAuthenticated()) {
      this.fetchConfig();
    }
    
    console.log('User Config Service initialized');
  }

  /**
   * Get current config
   */
  getConfig(): UserConfig | null {
    return this.config;
  }

  /**
   * Get save status information
   */
  getSaveStatus() {
    return {
      lastAttempt: this.lastSaveAttempt,
      lastSuccess: this.lastSaveSuccess,
      lastError: this.lastSaveError,
    };
  }

  /**
   * Fetch user configuration from server
   */
  async fetchConfig(): Promise<void> {
    if (!this.authService.isAuthenticated()) {
      console.log('Not fetching config: user not authenticated');
      return;
    }

    if (this.isDebugMode) {
      console.log('UserConfigService: Fetching user config');
    }

    try {
      const headers = this.getAuthHeaders();
        if (!headers) {
            if (this.isDebugMode) {
                console.log('UserConfigService: No auth token available, skipping config fetch');
            }
            return;
        }

      const response = await fetch(`${this.apiBaseUrl}${this.apiUrl}`, {
        method: 'GET',
        headers
      });

      if (!response.ok) {
        throw new Error(`HTTP ${response.status}: ${response.statusText}`);
      }

      const config: UserConfig = await response.json();
      this.config = config;

      if (this.isDebugMode) {
        console.log('UserConfigService: Config fetched successfully', config);
        const windowCount = config?.settings?.['windows']?.length || 0;
        console.log(`UserConfigService: Config contains ${windowCount} windows`);
      }

      // Emit config loaded event
      eventBus.emit('userconfig:loaded', config);

    } catch (error) {
      console.error('Error fetching user config:', error);
      eventBus.emit('userconfig:error', error);
    }
  }

  /**
   * Save user configuration to server
   */
  async saveConfig(settingsData: Record<string, any>): Promise<void> {
    if (!this.authService.isAuthenticated()) {
      console.log('Not saving config: user not authenticated');
      return;
    }

    // Update save attempt timestamp
    this.lastSaveAttempt = new Date();

    if (this.isDebugMode) {
      console.log('UserConfigService: Saving user config', settingsData);

      // Check specifically for windows data
      const windowsInSettings = settingsData?.['settings']?.['windows']?.length || 0;
      const windowsAtRoot = settingsData?.['windows']?.length || 0;

      if (windowsInSettings > 0) {
        console.log(`UserConfigService: Found ${windowsInSettings} windows in settings.windows structure`);
      }
      if (windowsAtRoot > 0) {
        console.log(`UserConfigService: Found ${windowsAtRoot} windows at root level`);
      }
    }

    // Prepare settings structure
    let updatedSettings: Record<string, any> = {};

    // If settingsData has nested 'settings', use that directly
    if (settingsData['settings']) {
      updatedSettings = settingsData['settings'];
    }
    // Otherwise, use the entire object as settings
    else {
      updatedSettings = settingsData;
    }

    // Build config object for server
    const config = {
      Settings: updatedSettings,
    };

    try {
      // Get fresh headers (the auth service will handle token refresh internally if needed)
      const headers = this.getAuthHeaders();
        if (!headers) {
            if (this.isDebugMode) {
                console.log('UserConfigService: No auth token available, skipping config save');
            }
            return;
        }

      if (this.isDebugMode) {
        console.log('UserConfigService: Using auth token for config save');
        console.log('UserConfigService: Saving config to server', {
          hasSettings: !!config.Settings,
          settingsKeys: Object.keys(config.Settings || {}),
        });
      }

      const response = await fetch(`${this.apiBaseUrl}${this.apiUrl}`, {
        method: 'POST',
        headers: {
          'Content-Type': 'application/json',
          ...headers
        },
        body: JSON.stringify(config)
      });

      if (!response.ok) {
        throw new Error(`HTTP ${response.status}: ${response.statusText}`);
      }

      const savedConfig: UserConfig = await response.json();
      this.config = savedConfig;
      this.lastSaveSuccess = new Date();
      this.lastSaveError = null;

      if (this.isDebugMode) {
        console.log('UserConfigService: Config saved successfully');
      }

      // Emit config saved event
      eventBus.emit('userconfig:saved', savedConfig);

    } catch (error) {
      const errorMessage = error instanceof Error ? error.message : 'Unknown error';
      this.lastSaveError = errorMessage;
      console.error('Error saving user config:', error);
      eventBus.emit('userconfig:error', error);
    }
  }

  /**
   * Save window configuration (debounced to prevent rate limiting)
   */
  async saveWindowConfig(windows: SavedWindow[]): Promise<void> {
    if (this.isDebugMode) {
      console.log('UserConfigService: Scheduling debounced window configuration save', { windowCount: windows.length });
    }

    // Clear existing timeout
    if (this.saveTimeout !== null) {
      clearTimeout(this.saveTimeout);
    }

    // Set new timeout for debounced save
    this.saveTimeout = window.setTimeout(async () => {
      try {
        if (this.isDebugMode) {
          console.log('UserConfigService: Executing debounced window configuration save');
        }

        // Update current config with new windows
        const currentSettings = this.config?.settings || {};
        const updatedSettings = {
          ...currentSettings,
          windows: windows
        };

        await this.saveConfig({ settings: updatedSettings });
      } catch (error) {
        console.warn('Debounced window config save failed:', error);
      } finally {
        this.saveTimeout = null;
      }
    }, this.saveDebounceMs);
  }

  /**
   * Get saved windows from config
   */
  getSavedWindows(): SavedWindow[] {
    const windows = this.config?.settings?.['windows'];
    if (Array.isArray(windows)) {
      return windows as SavedWindow[];
    }
    return [];
  }

  /**
   * Update specific setting
   */
  async updateSetting(key: string, value: any): Promise<void> {
    const currentSettings = this.config?.settings || {};
    const updatedSettings = {
      ...currentSettings,
      [key]: value
    };

    await this.saveConfig({ settings: updatedSettings });
  }

  /**
   * Get specific setting
   */
  getSetting<T = any>(key: string, defaultValue?: T): T {
    return this.config?.settings?.[key] ?? defaultValue;
  }

  /**
   * Clear configuration (on logout)
   */
  private clearConfig(): void {
    this.config = null;
    this.lastSaveAttempt = null;
    this.lastSaveSuccess = null;
    this.lastSaveError = null;
    
    if (this.isDebugMode) {
      console.log('UserConfigService: Config cleared');
    }

    eventBus.emit('userconfig:cleared');
  }

  /**
   * Get authentication headers
   */
    private getAuthHeaders(): Record<string, string> | null {
    const user = this.authService.getCurrentUser();
    if (!user || !user.token) {
        return null;
    }

    return {
      'Authorization': `Bearer ${user.token}`,
    };
  }

  /**
   * Dispose of the service
   */
  async dispose(): Promise<void> {
    // Clear any pending debounced save
    if (this.saveTimeout !== null) {
      clearTimeout(this.saveTimeout);
      this.saveTimeout = null;
    }

    // Clean up event listeners
    eventBus.off('auth:login', this.fetchConfig);
    eventBus.off('auth:logout', this.clearConfig);
    
    console.log('User Config Service disposed');
  }
}