/**
 * Window Manager Service
 * Manages window lifecycle, positioning, and state
 * Based on Angular WindowManagerService but framework-agnostic
 */

import type { 
  IWindow, 
  IWindowManager, 
  Position, 
  Size, 
  WindowRegistryEntry,
  WindowEventMap
} from '../core/types/index.js';
import { eventBus } from '../core/events/event-bus.js';
import { serviceContainer, SERVICE_TOKENS } from '../core/di/service-container.js';
import type { ComponentLoaderService } from '../core/services/component-loader.service.js';
import type { UserConfigService, SavedWindow } from '../core/services/user-config.service.js';

export class WindowManager implements IWindowManager {
  readonly name = 'WindowManager';
  
  private windows = new Map<string, IWindow>();
  private registry = new Map<string, WindowRegistryEntry>();
  private nextZIndex = 1000;
  private activeWindowId: string | null = null;

  // Debouncing for config saves
  private saveConfigTimeout: number | null = null;
  private readonly saveConfigDebounceMs = 1000; // Wait 1 second after last change

  /**
   * Initialize the window manager
   */
  async initialize(): Promise<void> {
    console.log('Initializing Window Manager...');
    
    // Register default window types
    this.registerDefaultWindows();
    
    // Setup user config integration
    this.setupUserConfigIntegration();
    
    console.log('Window Manager initialized');
  }

  /**
   * Register default window types
   */
  private registerDefaultWindows(): void {
    // Login window
    this.registerWindow('login', {
      defaultTitle: 'Login',
      defaultSize: { width: 400, height: 300 },
      defaultPosition: { x: 100, y: 100 },
      minSize: { width: 350, height: 250 },
      category: 'authentication',
      isDocked: false,
      isResizable: true,
      isDraggable: true
    });

    // Register window
    this.registerWindow('register', {
      defaultTitle: 'Create Account',
      defaultSize: { width: 450, height: 350 },
      defaultPosition: { x: 150, y: 150 },
      minSize: { width: 400, height: 300 },
      category: 'authentication',
      isDocked: false,
      isResizable: true,
      isDraggable: true
    });

    // File explorer
    this.registerWindow('file-explorer', {
      defaultTitle: 'File Explorer',
      defaultSize: { width: 800, height: 600 },
      defaultPosition: { x: 200, y: 100 },
      minSize: { width: 400, height: 300 },
      category: 'file-management',
      isDocked: true,
      isResizable: true,
      isDraggable: true
    });

    // Test Web Component
    this.registerWindow('test-web-component', {
      defaultTitle: 'Test Web Component',
      defaultSize: { width: 500, height: 400 },
      defaultPosition: { x: 250, y: 150 },
      minSize: { width: 400, height: 300 },
      category: 'development',
      isDocked: false,
      isResizable: true,
      isDraggable: true
    });

    // Todo App
    this.registerWindow('todo-app', {
      defaultTitle: 'Todo App',
      defaultSize: { width: 700, height: 800 },
      defaultPosition: { x: 300, y: 100 },
      minSize: { width: 500, height: 400 },
      category: 'productivity',
      isDocked: false,
      isResizable: true,
      isDraggable: true
    });

    console.log('Default windows registered');
  }

  /**
   * Register a window type
   */
  registerWindow(id: string, config: WindowRegistryEntry): void {
    this.registry.set(id, config);
  }

  /**
   * Create a new window
   */
  async createWindow(id: string, config: Partial<IWindow> = {}): Promise<IWindow> {
    if (this.windows.has(id)) {
      console.warn(`Window with id '${id}' already exists`);
      return this.windows.get(id)!;
    }

    const registryEntry = this.registry.get(id);
    if (!registryEntry) {
      throw new Error(`Window type '${id}' not registered`);
    }

    // Check for saved user configuration first
    const savedConfig = await this.getSavedWindowConfig(id);
    
    // Merge configurations with priority: savedConfig > passed config > registry defaults
    const finalConfig = {
      ...registryEntry,
      ...(savedConfig && {
        position: savedConfig.position,
        size: savedConfig.size,
        isMinimized: savedConfig.isMinimized,
        isFullscreen: savedConfig.isFullscreen,
        isTack: savedConfig.isTack,
        title: savedConfig.title || registryEntry.defaultTitle
      }),
      ...config
    };

    // Create window with merged configuration
    const window: IWindow = {
      id,
      title: finalConfig.title ?? finalConfig.defaultTitle,
      component: finalConfig.component ?? undefined,
      componentInputs: finalConfig.componentInputs ?? undefined,
      position: finalConfig.position ?? finalConfig.defaultPosition ?? this.getDefaultPosition(),
      size: finalConfig.size ?? finalConfig.defaultSize,
      minSize: finalConfig.minSize,
      maxSize: finalConfig.maxSize ?? undefined,
      isActive: false,
      isMinimized: finalConfig.isMinimized ?? false,
      isFullscreen: finalConfig.isFullscreen ?? false,
      isTack: finalConfig.isTack ?? false,
      zIndex: this.nextZIndex++,
      isResizable: finalConfig.isResizable ?? true,
      isDraggable: finalConfig.isDraggable ?? true,
      category: finalConfig.category ?? undefined
    };

    this.windows.set(id, window);

    // Create DOM element for window
    await this.createWindowElement(window);

    // Activate the new window
    this.activateWindow(id);

    // Emit window created event
    eventBus.emit('window:created', window);

    console.log(`Window '${id}' created with ${savedConfig ? 'saved' : 'default'} configuration`);
    return window;
  }

  /**
   * Create DOM element for window
   */
  private async createWindowElement(window: IWindow): Promise<void> {
    const windowElement = document.createElement('div');
    windowElement.id = `window-${window.id}`;
    windowElement.className = 'window';
    windowElement.dataset.windowId = window.id;

    // Apply initial styles
    this.applyWindowStyles(windowElement, window);

    // Create window structure
    this.createWindowStructure(windowElement, window);

    // Add to desktop container
    const desktop = document.querySelector('.desktop-container');
    if (desktop) {
      desktop.appendChild(windowElement);
    } else {
      console.error('Desktop container not found');
    }

    // Load component if needed
    await this.loadWindowComponent(window);
  }

  /**
   * Load and mount component for window
   */
  private async loadWindowComponent(window: IWindow): Promise<void> {
    try {
      const componentLoader = serviceContainer.get<ComponentLoaderService>(SERVICE_TOKENS.COMPONENT_LOADER);
      
      // Check if we should load a component for this window type
      if (componentLoader.hasComponent(window.id)) {
        const contentContainer = document.getElementById(`window-content-${window.id}`);
        if (!contentContainer) {
          console.error(`Content container not found for window '${window.id}'`);
          return;
        }

        // Load and mount the component
        const component = await componentLoader.loadComponent(
          window.id, 
          contentContainer, 
          window.componentInputs
        );
        
        if (component) {
          await component.mount();
          console.log(`Component loaded for window '${window.id}'`);
          
          // Store component reference on window for cleanup later
          (window as any).componentInstance = component;
        }
      } else {
        console.log(`No component registered for window type '${window.id}'`);
      }
    } catch (error) {
      console.error(`Failed to load component for window '${window.id}':`, error);
    }
  }

  /**
   * Apply styles to window element
   */
  private applyWindowStyles(element: HTMLElement, window: IWindow): void {
    const style = element.style;
    style.position = 'absolute';
    style.left = `${window.position.x}px`;
    style.top = `${window.position.y}px`;
    style.width = `${window.size.width}px`;
    style.height = `${window.size.height}px`;
    style.zIndex = window.zIndex.toString();
    style.display = window.isMinimized ? 'none' : 'flex';
    
    // Add CSS classes for states
    element.classList.toggle('active', window.isActive);
    element.classList.toggle('fullscreen', window.isFullscreen);
    element.classList.toggle('minimized', window.isMinimized);
  }

  /**
   * Create window structure (titlebar, content, resize handles)
   */
  private createWindowStructure(element: HTMLElement, window: IWindow): void {
    // Window titlebar
    const titlebar = document.createElement('div');
    titlebar.className = 'window-titlebar';
    
    // macOS-style controls
    const controls = document.createElement('div');
    controls.className = 'window-controls macos-controls';
    
    // Close button
    const closeBtn = document.createElement('button');
    closeBtn.className = 'window-control close';
    closeBtn.addEventListener('click', () => this.closeWindow(window.id));
    controls.appendChild(closeBtn);
    
    // Maximize button
    const maxBtn = document.createElement('button');
    maxBtn.className = 'window-control maximize';
    maxBtn.addEventListener('click', () => this.toggleFullscreen(window.id));
    controls.appendChild(maxBtn);
    
    titlebar.appendChild(controls);
    
    // Window title
    const title = document.createElement('div');
    title.className = 'window-title';
    title.textContent = window.title;
    titlebar.appendChild(title);
    
    // Tack button
    const tackBtn = document.createElement('button');
    tackBtn.className = 'window-control tack';
    tackBtn.addEventListener('click', () => this.toggleTack(window.id));
    titlebar.appendChild(tackBtn);
    
    element.appendChild(titlebar);
    
    // Window content
    const content = document.createElement('div');
    content.className = 'window-content';
    content.id = `window-content-${window.id}`;
    element.appendChild(content);
    
    // Add resize handles
    if (window.isResizable) {
      this.addResizeHandles(element, window);
    }
    
    // Add drag functionality
    if (window.isDraggable) {
      this.addDragFunctionality(titlebar, window);
    }
  }

  /**
   * Add resize handles to window
   */
  private addResizeHandles(element: HTMLElement, window: IWindow): void {
    const handles = ['n', 'e', 's', 'w', 'ne', 'se', 'sw', 'nw'];
    
    for (const handle of handles) {
      const resizeHandle = document.createElement('div');
      resizeHandle.className = `resize-handle resize-${handle}`;
      resizeHandle.addEventListener('mousedown', (e) => this.startResize(e, window, handle));
      element.appendChild(resizeHandle);
    }
  }

  /**
   * Add drag functionality to titlebar
   */
  private addDragFunctionality(titlebar: HTMLElement, window: IWindow): void {
    let isDragging = false;
    let dragStart = { x: 0, y: 0 };
    let windowStart = { x: 0, y: 0 };

    const startDrag = (e: MouseEvent) => {
      if (window.isFullscreen) return;
      
      isDragging = true;
      dragStart = { x: e.clientX, y: e.clientY };
      windowStart = { ...window.position };
      
      this.activateWindow(window.id);
      e.preventDefault();
    };

    const doDrag = (e: MouseEvent) => {
      if (!isDragging) return;
      
      const dx = e.clientX - dragStart.x;
      const dy = e.clientY - dragStart.y;
      
      const newPosition = {
        x: windowStart.x + dx,
        y: Math.max(0, windowStart.y + dy) // Don't go above menu bar
      };
      
      this.moveWindow(window.id, newPosition);
    };

    const endDrag = () => {
      if (isDragging) {
        isDragging = false;
        // Use the debounced save that integrates with UserConfigService
        this.debouncedSaveWindowConfigurations();
      }
    };

    titlebar.addEventListener('mousedown', startDrag);
    document.addEventListener('mousemove', doDrag);
    document.addEventListener('mouseup', endDrag);

    // Double-click to toggle fullscreen
    titlebar.addEventListener('dblclick', () => this.toggleFullscreen(window.id));
  }

  /**
   * Start window resize
   */
  private startResize(e: MouseEvent, window: IWindow, direction: string): void {
    if (window.isFullscreen) return;

    let isResizing = true;
    const startX = e.clientX;
    const startY = e.clientY;
    const startSize = { ...window.size };
    const startPos = { ...window.position };

    const doResize = (e: MouseEvent) => {
      if (!isResizing) return;

      const dx = e.clientX - startX;
      const dy = e.clientY - startY;

      let newSize = { ...startSize };
      let newPos = { ...startPos };

      // Calculate new size and position based on resize direction
      switch (direction) {
        case 'e':
          newSize.width = Math.max(window.minSize.width, startSize.width + dx);
          break;
        case 'w':
          newSize.width = Math.max(window.minSize.width, startSize.width - dx);
          newPos.x = startPos.x + (startSize.width - newSize.width);
          break;
        case 's':
          newSize.height = Math.max(window.minSize.height, startSize.height + dy);
          break;
        case 'n':
          newSize.height = Math.max(window.minSize.height, startSize.height - dy);
          newPos.y = Math.max(0, startPos.y + (startSize.height - newSize.height));
          break;
        case 'se':
          newSize.width = Math.max(window.minSize.width, startSize.width + dx);
          newSize.height = Math.max(window.minSize.height, startSize.height + dy);
          break;
        case 'sw':
          newSize.width = Math.max(window.minSize.width, startSize.width - dx);
          newSize.height = Math.max(window.minSize.height, startSize.height + dy);
          newPos.x = startPos.x + (startSize.width - newSize.width);
          break;
        case 'ne':
          newSize.width = Math.max(window.minSize.width, startSize.width + dx);
          newSize.height = Math.max(window.minSize.height, startSize.height - dy);
          newPos.y = Math.max(0, startPos.y + (startSize.height - newSize.height));
          break;
        case 'nw':
          newSize.width = Math.max(window.minSize.width, startSize.width - dx);
          newSize.height = Math.max(window.minSize.height, startSize.height - dy);
          newPos.x = startPos.x + (startSize.width - newSize.width);
          newPos.y = Math.max(0, startPos.y + (startSize.height - newSize.height));
          break;
      }

      this.resizeWindow(window.id, newSize);
      if (newPos.x !== window.position.x || newPos.y !== window.position.y) {
        this.moveWindow(window.id, newPos);
      }
    };

    const endResize = () => {
      if (isResizing) {
        isResizing = false;
        // Use the debounced save that integrates with UserConfigService
        this.debouncedSaveWindowConfigurations();
        document.removeEventListener('mousemove', doResize);
        document.removeEventListener('mouseup', endResize);
      }
    };

    document.addEventListener('mousemove', doResize);
    document.addEventListener('mouseup', endResize);
    e.preventDefault();
  }

  /**
   * Get a window by ID
   */
  getWindow(id: string): IWindow | undefined {
    return this.windows.get(id);
  }

  /**
   * Get all windows
   */
  getAllWindows(): IWindow[] {
    return Array.from(this.windows.values());
  }

  /**
   * Get visible windows
   */
  getVisibleWindows(): IWindow[] {
    return this.getAllWindows().filter(w => !w.isMinimized);
  }

  /**
   * Get minimized windows
   */
  getMinimizedWindows(): IWindow[] {
    return this.getAllWindows().filter(w => w.isMinimized);
  }

  /**
   * Activate a window
   */
  activateWindow(id: string): void {
    const window = this.windows.get(id);
    if (!window) return;

    // Deactivate all other windows
    for (const w of this.windows.values()) {
      if (w.id !== id && w.isActive) {
        w.isActive = false;
        this.updateWindowElement(w);
      }
    }

    // Activate target window
    window.isActive = true;
    window.zIndex = this.nextZIndex++;
    this.activeWindowId = id;

    this.updateWindowElement(window);
    eventBus.emit('window:activated', window);
  }

  /**
   * Minimize a window
   */
  minimizeWindow(id: string): void {
    const window = this.windows.get(id);
    if (!window) return;

    window.isMinimized = true;
    window.isActive = false;

    this.updateWindowElement(window);
    eventBus.emit('window:minimized', window);

    // Activate next window
    const visibleWindows = this.getVisibleWindows();
    if (visibleWindows.length > 0 && visibleWindows[0]) {
      this.activateWindow(visibleWindows[0].id);
    }
  }

  /**
   * Maximize/restore a window
   */
  maximizeWindow(id: string): void {
    this.toggleFullscreen(id);
  }

  /**
   * Toggle fullscreen state
   */
  toggleFullscreen(id: string): void {
    const window = this.windows.get(id);
    if (!window) return;

    window.isFullscreen = !window.isFullscreen;
    this.updateWindowElement(window);

    if (window.isFullscreen) {
      eventBus.emit('window:maximized', window);
    } else {
      eventBus.emit('window:restored', window);
    }
  }

  /**
   * Toggle tack (pin) state
   */
  toggleTack(id: string): void {
    const window = this.windows.get(id);
    if (!window) return;

    window.isTack = !window.isTack;
    this.updateWindowElement(window);
  }

  /**
   * Close a window
   */
  closeWindow(id: string): void {
    const window = this.windows.get(id);
    if (!window) return;

    // Clean up component if it exists
    const componentInstance = (window as any).componentInstance;
    if (componentInstance && typeof componentInstance.unmount === 'function') {
      try {
        const unmountResult = componentInstance.unmount();
        // Handle both synchronous and asynchronous unmount
        if (unmountResult && typeof unmountResult.catch === 'function') {
          unmountResult.catch((error: Error) => {
            console.error(`Error unmounting component for window '${id}':`, error);
          });
        }
      } catch (error) {
        console.error(`Error unmounting component for window '${id}':`, error);
      }
    }

    // Remove DOM element
    const element = document.getElementById(`window-${id}`);
    if (element) {
      element.remove();
    }

    // Remove from windows map
    this.windows.delete(id);

    eventBus.emit('window:closed', window);

    // Activate next window
    if (this.activeWindowId === id) {
      this.activeWindowId = null;
      const visibleWindows = this.getVisibleWindows();
      if (visibleWindows.length > 0 && visibleWindows[0]) {
        this.activateWindow(visibleWindows[0].id);
      }
    }

    console.log(`Window '${id}' closed`);
  }

  /**
   * Move a window
   */
  moveWindow(id: string, position: Position): void {
    const window = this.windows.get(id);
    if (!window) return;

    window.position = position;
    this.updateWindowElement(window);
    eventBus.emit('window:moved', window, position);
  }

  /**
   * Resize a window
   */
  resizeWindow(id: string, size: Size): void {
    const window = this.windows.get(id);
    if (!window) return;

    window.size = size;
    this.updateWindowElement(window);
    eventBus.emit('window:resized', window, size);
  }

  /**
   * Set window z-index
   */
  setWindowZIndex(id: string, zIndex: number): void {
    const window = this.windows.get(id);
    if (!window) return;

    window.zIndex = zIndex;
    this.updateWindowElement(window);
  }

  /**
   * Update window DOM element
   */
  private updateWindowElement(window: IWindow): void {
    const element = document.getElementById(`window-${window.id}`);
    if (!element) return;

    this.applyWindowStyles(element, window);

    // Update title
    const titleElement = element.querySelector('.window-title');
    if (titleElement) {
      titleElement.textContent = window.title;
    }

    // Update tack button state
    const tackButton = element.querySelector('.window-control.tack');
    if (tackButton) {
      tackButton.classList.toggle('pinned', window.isTack);
    }
  }

  /**
   * Get default position for new windows
   */
  private getDefaultPosition(): Position {
    const offset = this.windows.size * 30;
    return { x: 100 + offset, y: 100 + offset };
  }

  /**
   * Load window configurations from localStorage
   */
  private loadWindowConfigurations(): void {
    try {
      const saved = localStorage.getItem('vfs-window-configs');
      if (saved) {
        const configs = JSON.parse(saved);
        console.log('Loaded window configurations from storage');
      }
    } catch (error) {
      console.warn('Failed to load window configurations:', error);
    }
  }

  /**
   * Save window configuration to localStorage
   */
  private saveWindowConfiguration(id: string): void {
    try {
      const window = this.windows.get(id);
      if (!window) return;

      const saved = localStorage.getItem('vfs-window-configs') || '{}';
      const configs = JSON.parse(saved);
      
      configs[id] = {
        position: window.position,
        size: window.size,
        isFullscreen: window.isFullscreen
      };

      localStorage.setItem('vfs-window-configs', JSON.stringify(configs));
    } catch (error) {
      console.warn('Failed to save window configuration:', error);
    }
  }

  /**
   * Get window registry
   */
  getWindowRegistry(): Map<string, WindowRegistryEntry> {
    return this.registry;
  }

  /**
   * Setup user config integration
   */
  private setupUserConfigIntegration(): void {
    try {
      // Listen for user config events
      eventBus.on('userconfig:loaded', () => this.restoreWindowsFromConfig());
      eventBus.on('auth:logout', () => this.clearWindowState());

      // Save window configurations when windows change (debounced)
      eventBus.on('window:moved', () => this.debouncedSaveWindowConfigurations());
      eventBus.on('window:resized', () => this.debouncedSaveWindowConfigurations());
      eventBus.on('window:closed', () => this.debouncedSaveWindowConfigurations());

      console.log('Window Manager: User config integration setup complete');
    } catch (error) {
      console.warn('Window Manager: Failed to setup user config integration:', error);
    }
  }

  /**
   * Restore windows from user configuration
   */
  private async restoreWindowsFromConfig(): Promise<void> {
    try {
      const userConfigService = serviceContainer.get<UserConfigService>(SERVICE_TOKENS.USER_CONFIG_SERVICE);
      const savedWindows = userConfigService.getSavedWindows();

      if (savedWindows.length > 0) {
        console.log(`Window Manager: Restoring ${savedWindows.length} windows from config`);

        for (const savedWindow of savedWindows) {
          // Only restore windows that aren't already open
          if (!this.windows.has(savedWindow.id)) {
            try {
              await this.createWindow(savedWindow.id, {
                ...(savedWindow.title && { title: savedWindow.title }),
                position: savedWindow.position,
                size: savedWindow.size,
                ...(savedWindow.isMinimized !== undefined && { isMinimized: savedWindow.isMinimized }),
                ...(savedWindow.isFullscreen !== undefined && { isFullscreen: savedWindow.isFullscreen }),
                ...(savedWindow.isTack !== undefined && { isTack: savedWindow.isTack }),
                ...(savedWindow.componentInputs && { componentInputs: savedWindow.componentInputs })
              });
            } catch (error) {
              console.warn(`Failed to restore window '${savedWindow.id}':`, error);
            }
          }
        }
      }
    } catch (error) {
      console.warn('Window Manager: Failed to restore windows from config:', error);
    }
  }

  /**
   * Save current window configurations to user config
   */
  private async saveWindowConfigurations(): Promise<void> {
    try {
      const userConfigService = serviceContainer.get<UserConfigService>(SERVICE_TOKENS.USER_CONFIG_SERVICE);

      const savedWindows: SavedWindow[] = [];

      for (const window of this.windows.values()) {
        savedWindows.push({
          id: window.id,
          title: window.title,
          position: window.position,
          size: window.size,
          minSize: window.minSize,
          zIndex: window.zIndex,
          isMinimized: window.isMinimized,
          isFullscreen: window.isFullscreen,
          isTack: window.isTack,
          componentInputs: window.componentInputs || {}
        });
      }

      await userConfigService.saveWindowConfig(savedWindows);
      console.log(`Window Manager: Saved ${savedWindows.length} window configurations to UserConfig`);
    } catch (error) {
      console.warn('Window Manager: Failed to save window configurations:', error);
    }
  }

  /**
   * Debounced save to prevent too many requests during window operations
   */
  private debouncedSaveWindowConfigurations(): void {
    // Only log occasionally to avoid console spam
    if (!this.saveConfigTimeout) {
      console.log('Window Manager: Debounced save triggered');
    }
    
    // Clear existing timeout
    if (this.saveConfigTimeout !== null) {
      clearTimeout(this.saveConfigTimeout);
    }

    // Set new timeout
    this.saveConfigTimeout = window.setTimeout(() => {
      this.saveWindowConfigurations().catch(error => {
        console.warn('Debounced save failed:', error);
      });
      this.saveConfigTimeout = null;
    }, this.saveConfigDebounceMs);
  }

  /**
   * Clear window state (on logout)
   */
  private clearWindowState(): void {
    // Close all windows but don't save the state
    for (const id of this.windows.keys()) {
      this.closeWindow(id);
    }
    console.log('Window Manager: Window state cleared');
  }

  /**
   * Get saved window configuration for a specific window ID
   */
  private async getSavedWindowConfig(windowId: string): Promise<SavedWindow | null> {
    try {
      const userConfigService = serviceContainer.get<UserConfigService>(SERVICE_TOKENS.USER_CONFIG_SERVICE);
      const savedWindows = userConfigService.getSavedWindows();
      
      const savedWindow = savedWindows.find(w => w.id === windowId);
      if (savedWindow) {
        console.log(`Window Manager: Found saved config for '${windowId}':`, savedWindow);
      } else {
        console.log(`Window Manager: No saved config found for '${windowId}'`);
      }
      return savedWindow || null;
    } catch (error) {
      console.warn(`Failed to get saved config for window '${windowId}':`, error);
      return null;
    }
  }

  /**
   * Destroy the window manager
   */
  async destroy(): Promise<void> {
    // Clear any pending debounced save
    if (this.saveConfigTimeout !== null) {
      clearTimeout(this.saveConfigTimeout);
      this.saveConfigTimeout = null;
    }

    // Clean up event listeners
    eventBus.off('userconfig:loaded', this.restoreWindowsFromConfig);
    eventBus.off('auth:logout', this.clearWindowState);
    eventBus.off('window:moved', this.debouncedSaveWindowConfigurations);
    eventBus.off('window:resized', this.debouncedSaveWindowConfigurations);
    eventBus.off('window:closed', this.debouncedSaveWindowConfigurations);

    // Close all windows
    for (const id of this.windows.keys()) {
      this.closeWindow(id);
    }

    this.windows.clear();
    this.registry.clear();
    this.activeWindowId = null;

    console.log('Window Manager destroyed');
  }
}