/**
 * Main entry point for the VFS Desktop application
 * Initializes all core services and starts the desktop environment
 */

import { serviceContainer, SERVICE_TOKENS } from './core/di/service-container.js';
import { eventBus } from './core/events/event-bus.js';
import { WindowManager } from './windowing/window-manager.js';
import { DesktopService } from './desktop/desktop-service.js';
import { AuthService } from './auth/auth-service.js';
import { ThemeService } from './services/theme-service.js';
import { ComponentLoaderService } from './core/services/component-loader.service.js';
import { UserConfigService } from './core/services/user-config.service.js';
import { Desktop } from './desktop/desktop.js';
import { componentDiscovery } from './components/component-discovery.js';
import type { VFSConfig } from './core/types/index.js';

// Default configuration
const defaultConfig: VFSConfig = {
  theme: 'light',
  osStyle: 'macos',
  enableAnimations: true,
  animationDuration: 300,
  defaultWindowSize: { width: 800, height: 600 },
  apiBaseUrl: '/api',
  storagePrefix: 'vfs-'
};

/**
 * VFS Desktop Application class
 */
export class VFSDesktop {
  private desktop?: Desktop | undefined;
  private initialized = false;

  constructor(private config: Partial<VFSConfig> = {}) {
    this.config = { ...defaultConfig, ...config };
  }

  /**
   * Initialize the desktop application
   */
  async initialize(): Promise<void> {
    if (this.initialized) {
      console.warn('VFS Desktop already initialized');
      return;
    }

    try {
      console.log('Initializing VFS Desktop...');

      // Register core services first
      this.registerServices();

      // Initialize component discovery system after services are registered
      await componentDiscovery.initialize();

      // Initialize all services
      await serviceContainer.initializeAll();

      // Emit services initialized event for component discovery retry
      eventBus.emit('services:initialized');

      // Create and mount desktop
      const desktopContainer = document.getElementById('desktop-container');
      if (!desktopContainer) {
        throw new Error('Desktop container element not found');
      }

      this.desktop = new Desktop(desktopContainer);
      await this.desktop.mount();

      // Apply initial theme
      const themeService = serviceContainer.get<ThemeService>(SERVICE_TOKENS.THEME_SERVICE);
      themeService.setTheme(this.config.theme!);

      // Set initial OS style
      const desktopService = serviceContainer.get<DesktopService>(SERVICE_TOKENS.DESKTOP_SERVICE);
      desktopService.setOSStyle(this.config.osStyle!);

      this.initialized = true;
      console.log('VFS Desktop initialized successfully');

      // Emit initialization complete event
      eventBus.emit('desktop:initialized', this);

    } catch (error) {
      console.error('Failed to initialize VFS Desktop:', error);
      throw error;
    }
  }

  /**
   * Register all core services with the DI container
   */
  private registerServices(): void {
    // Core services
    serviceContainer.register(SERVICE_TOKENS.WINDOW_MANAGER, WindowManager);
    serviceContainer.register(SERVICE_TOKENS.DESKTOP_SERVICE, DesktopService);
    serviceContainer.register(SERVICE_TOKENS.AUTH_SERVICE, AuthService);
    serviceContainer.register(SERVICE_TOKENS.THEME_SERVICE, ThemeService);
    serviceContainer.register(SERVICE_TOKENS.COMPONENT_LOADER, ComponentLoaderService);
    serviceContainer.register(SERVICE_TOKENS.USER_CONFIG_SERVICE, UserConfigService);

    console.log('Core services registered');
  }

  /**
   * Destroy the desktop application
   */
  async destroy(): Promise<void> {
    if (!this.initialized) {
      return;
    }

    try {
      console.log('Destroying VFS Desktop...');

      // Unmount desktop
      if (this.desktop) {
        await this.desktop.unmount();
        this.desktop = undefined;
      }

      // Destroy all services
      await serviceContainer.destroyAll();

      // Clear event listeners
      eventBus.removeAllListeners();

      this.initialized = false;
      console.log('VFS Desktop destroyed successfully');

    } catch (error) {
      console.error('Error destroying VFS Desktop:', error);
      throw error;
    }
  }

  /**
   * Get the current configuration
   */
  getConfig(): VFSConfig {
    return this.config as VFSConfig;
  }

  /**
   * Check if desktop is initialized
   */
  isInitialized(): boolean {
    return this.initialized;
  }
}

/**
 * Initialize the desktop when DOM is ready
 */
async function initializeDesktop(): Promise<void> {
  // Wait for DOM to be ready
  if (document.readyState === 'loading') {
    await new Promise(resolve => {
      document.addEventListener('DOMContentLoaded', resolve, { once: true });
    });
  }

  // Create and initialize desktop
  const desktop = new VFSDesktop();
  
  // Make desktop globally available for debugging
  (window as any).vfsDesktop = desktop;

  try {
    await desktop.initialize();
  } catch (error) {
    console.error('Failed to initialize desktop:', error);
    
    // Show error message to user
    const errorDiv = document.createElement('div');
    errorDiv.style.cssText = `
      position: fixed;
      top: 50%;
      left: 50%;
      transform: translate(-50%, -50%);
      background: #ef4444;
      color: white;
      padding: 5px;
      border-radius: 8px;
      font-family: system-ui, sans-serif;
      z-index: 10000;
    `;
    errorDiv.textContent = `Failed to initialize VFS Desktop: ${error instanceof Error ? error.message : 'Unknown error'}`;
    document.body.appendChild(errorDiv);
  }
}

// Auto-initialize
initializeDesktop().catch(console.error);

// Export for manual initialization if needed
export { initializeDesktop };