/**
 * Desktop Service
 * Manages desktop applications, OS style, and desktop-level functionality
 */

import type { 
  IDesktopService, 
  DesktopApp, 
  OSStyle, 
  Theme 
} from '../core/types/index.js';
import { eventBus } from '../core/events/event-bus.js';
import { serviceContainer, SERVICE_TOKENS } from '../core/di/service-container.js';
import type { IWindowManager } from '../core/types/index.js';

export class DesktopService implements IDesktopService {
  readonly name = 'DesktopService';
  
  private apps: DesktopApp[] = [];
  private osStyle: OSStyle = 'macos';
  private theme: Theme = 'light';

  /**
   * Initialize the desktop service
   */
  async initialize(): Promise<void> {
    console.log('Initializing Desktop Service...');
    
    // Load preferences from localStorage
    this.loadPreferences();
    
    // Register default apps
    this.registerDefaultApps();
    
    console.log('Desktop Service initialized');
  }

  /**
   * Load user preferences from localStorage
   */
  private loadPreferences(): void {
    try {
      const osStyle = localStorage.getItem('vfs-os-style') as OSStyle;
      if (osStyle === 'macos' || osStyle === 'windows') {
        this.osStyle = osStyle;
      }

      const theme = localStorage.getItem('vfs-theme') as Theme;
      if (theme === 'light' || theme === 'dark') {
        this.theme = theme;
      }
    } catch (error) {
      console.warn('Failed to load preferences:', error);
    }
  }

  /**
   * Register default desktop applications
   */
  private registerDefaultApps(): void {
    this.apps = [
      {
        id: 'file-explorer',
        name: 'File Explorer',
        icon: 'folder',
        category: 'file-management'
      },
      {
        id: 'login',
        name: 'Login',
        icon: 'login',
        category: 'authentication'
      },
      {
        id: 'register',
        name: 'Register',
        icon: 'person_add',
        category: 'authentication'
      },
      {
        id: 'settings',
        name: 'Settings',
        icon: 'settings',
        category: 'system'
      },
      {
        id: 'terminal',
        name: 'Terminal',
        icon: 'terminal',
        category: 'development'
      },
      {
        id: 'text-editor',
        name: 'Text Editor',
        icon: 'edit',
        category: 'productivity'
      },
      {
        id: 'calculator',
        name: 'Calculator',
        icon: 'calculate',
        category: 'utilities'
      },
      {
        id: 'chat',
        name: 'Chat',
        icon: 'chat',
        category: 'communication'
      },
      {
        id: 'test-web-component',
        name: 'Test Web Component',
        icon: 'code',
        category: 'development'
      },
      {
        id: 'todo-app',
        name: 'Todo App',
        icon: 'check_circle',
        category: 'productivity'
      }
    ];

    console.log(`Registered ${this.apps.length} desktop applications`);
  }

  /**
   * Get all registered desktop applications
   */
  getApps(): DesktopApp[] {
    return [...this.apps];
  }

  /**
   * Launch an application by ID
   */
  async launchApp(appId: string): Promise<void> {
    const app = this.apps.find(a => a.id === appId);
    if (!app) {
      throw new Error(`Application '${appId}' not found`);
    }

    try {
      // Get window manager
      const windowManager = serviceContainer.get<IWindowManager>(SERVICE_TOKENS.WINDOW_MANAGER);
      
      // Check if window already exists
      const existingWindow = windowManager.getWindow(appId);
      if (existingWindow) {
        // If minimized, restore it
        if (existingWindow.isMinimized) {
          windowManager.activateWindow(appId);
        } else {
          // Just activate the existing window
          windowManager.activateWindow(appId);
        }
        return;
      }

      // Create new window for the app
      const window = await windowManager.createWindow(appId, {
        title: app.name
      });

      // Load app component if available
      if (app.component) {
        await this.loadAppComponent(window.id, app.component);
      }

      // Emit app launched event
      eventBus.emit('desktop:app-launched', appId, app);
      
      console.log(`Launched application: ${app.name}`);

    } catch (error) {
      console.error(`Failed to launch application '${appId}':`, error);
      throw error;
    }
  }

  /**
   * Load component for an app window
   */
  private async loadAppComponent(windowId: string, componentConstructor: any): Promise<void> {
    try {
      const contentElement = document.getElementById(`window-content-${windowId}`);
      if (!contentElement) {
        throw new Error(`Window content element not found for window '${windowId}'`);
      }

      // Create component instance
      const component = new componentConstructor(contentElement);
      
      // Mount the component
      if (component.mount) {
        await component.mount();
      }

      console.log(`Component loaded for window '${windowId}'`);

    } catch (error) {
      console.error(`Failed to load component for window '${windowId}':`, error);
      throw error;
    }
  }

  /**
   * Check if an application is currently active (has an open window)
   */
  isAppActive(appId: string): boolean {
    const windowManager = serviceContainer.get<IWindowManager>(SERVICE_TOKENS.WINDOW_MANAGER);
    const window = windowManager.getWindow(appId);
    return window !== undefined && !window.isMinimized;
  }

  /**
   * Get current OS style
   */
  getOSStyle(): OSStyle {
    return this.osStyle;
  }

  /**
   * Set OS style (macOS or Windows)
   */
  setOSStyle(style: OSStyle): void {
    if (this.osStyle === style) return;

    this.osStyle = style;
    
    // Save to localStorage
    try {
      localStorage.setItem('vfs-os-style', style);
    } catch (error) {
      console.warn('Failed to save OS style preference:', error);
    }

    // Update desktop UI
    this.updateDesktopForOSStyle(style);

    // Emit change event
    eventBus.emit('desktop:os-style-changed', style);
    
    console.log(`OS style changed to: ${style}`);
  }

  /**
   * Update desktop UI for OS style
   */
  private updateDesktopForOSStyle(style: OSStyle): void {
    const desktop = document.querySelector('.desktop-container');
    if (desktop) {
      desktop.classList.remove('os-macos', 'os-windows');
      desktop.classList.add(`os-${style}`);
    }
  }

  /**
   * Get current theme
   */
  getTheme(): Theme {
    return this.theme;
  }

  /**
   * Set theme (light or dark)
   */
  setTheme(theme: Theme): void {
    if (this.theme === theme) return;

    this.theme = theme;
    
    // Save to localStorage
    try {
      localStorage.setItem('vfs-theme', theme);
    } catch (error) {
      console.warn('Failed to save theme preference:', error);
    }

    // Apply theme to document
    document.documentElement.setAttribute('data-theme', theme);

    // Emit change event
    eventBus.emit('desktop:theme-changed', theme);
    
    console.log(`Theme changed to: ${theme}`);
  }

  /**
   * Toggle between light and dark theme
   */
  toggleTheme(): void {
    const newTheme = this.theme === 'light' ? 'dark' : 'light';
    this.setTheme(newTheme);
  }

  /**
   * Register a new desktop application
   */
  registerApp(app: DesktopApp): void {
    const existingIndex = this.apps.findIndex(a => a.id === app.id);
    if (existingIndex >= 0) {
      this.apps[existingIndex] = app;
    } else {
      this.apps.push(app);
    }
    
    console.log(`Registered application: ${app.name}`);
  }

  /**
   * Unregister a desktop application
   */
  unregisterApp(appId: string): void {
    const index = this.apps.findIndex(a => a.id === appId);
    if (index >= 0) {
      const app = this.apps[index];
      this.apps.splice(index, 1);
      if (app) {
        console.log(`Unregistered application: ${app.name}`);
      }
    }
  }

  /**
   * Get applications by category
   */
  getAppsByCategory(category: string): DesktopApp[] {
    return this.apps.filter(app => app.category === category);
  }

  /**
   * Get all application categories
   */
  getCategories(): string[] {
    const categories = new Set(this.apps.map(app => app.category).filter((cat): cat is string => Boolean(cat)));
    return Array.from(categories).sort();
  }

  /**
   * Search applications by name
   */
  searchApps(query: string): DesktopApp[] {
    const lowerQuery = query.toLowerCase();
    return this.apps.filter(app => 
      app.name.toLowerCase().includes(lowerQuery) ||
      app.id.toLowerCase().includes(lowerQuery) ||
      (app.category && app.category.toLowerCase().includes(lowerQuery))
    );
  }

  /**
   * Get application by ID
   */
  getApp(appId: string): DesktopApp | undefined {
    return this.apps.find(app => app.id === appId);
  }

  /**
   * Get applications for dock/taskbar (apps that should be visible in the dock)
   */
  getDockedApps(): DesktopApp[] {
    // Return main applications that should always be in dock
    return this.apps.filter(app => 
      ['file-explorer', 'terminal', 'text-editor', 'calculator', 'chat', 'settings', 'test-web-component', 'todo-app'].includes(app.id)
    );
  }

  /**
   * Destroy the desktop service
   */
  async destroy(): Promise<void> {
    this.apps = [];
    console.log('Desktop Service destroyed');
  }
}